<template>
    <div class="list-header">
        <div class="search-container">
            <div class="search-input-wrapper" :class="{ focus: isFocused }">
                <svg class="search-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                    <path :d="searchIconPath"></path>
                </svg>
                <input
                    type="text"
                    ref="searchInput"
                    v-model.trim="searchValue"
                    placeholder=""
                    class="actual-search-input"
                    @focus="handleFocus"
                    @blur="handleBlur"
                    @keyup.enter="handleSearchSubmit"
                />
                <div v-if="searchValue.length > 0 && isFocused" class="clear-box"
                    @mousedown.stop.prevent="clearSearchInput()">
                    <svg class="clear-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                        <path :d="clearIconPath"></path>
                    </svg>
                </div>
            </div>
            <button class="add-button" @click="openAddFriendOverlay()">
                <svg class="add-icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                    <path :d="addIconPath" />
                </svg>
            </button>
        </div>

        <div v-if="showSearchResultsOverlay" class="search-results-overlay">
            <template v-if="filteredFriends.length > 0">
                <p class="search-results-category"></p>
                <ul class="search-results-list">
                    <li v-for="user in filteredFriends" :key="user.user_id"
                        @click="selectUserFromSearch(user)"
                        class="search-user-item"
                        :class="{'highlighted': (filteredFriends.length > 0 && highlightFirstFriend)}"
                    >
                        <img :src="user.avatar" class="avatar" :alt="user.name + ''" />
                        <div class="user-info">
                            <span class="name">{{ user.nickname || user.name }}</span>
                        </div>
                    </li>
                </ul>
            </template>

            <template v-if="isValidEmail && (remoteSearchLoading || searchedNewUser || remoteSearchError)">
                <p class="search-results-category"></p>
                <ul class="search-results-list">
                    <li v-if="remoteSearchLoading" class="loading-item search-item">...</li>
                    <li v-else-if="remoteSearchError" class="empty-list search-item error-message">{{ remoteSearchError }}</li>
                    <li v-else-if="searchedNewUser" class="search-user-item new-user-item"
                        :class="{'highlighted': highlightNewUserItem}"
                        @click="addNewFriend(searchedNewUser)"
                    >
                        <img :src="searchedNewUser.avatar" class="avatar" :alt="searchedNewUser.name + ''" />
                        <div class="user-info">
                            <span class="name">{{ searchedNewUser.nickname || searchedNewUser.name }}</span>
                            <span class="email">{{ searchedNewUser.email }}</span>
                        </div>
                        <button class="add-button-action" @click.stop="addNewFriend(searchedNewUser)"></button>
                    </li>
                    <li v-else-if="!searchedNewUser && !remoteSearchError" class="empty-list search-item"></li>
                </ul>
            </template>
            <template v-else-if="!isValidEmail && searchValue.length > 0 && filteredFriends.length === 0">
                <p class="empty-list search-item"></p>
            </template>
        </div>

        <div v-if="showAddFriendOverlay" class="add-friend-overlay">
            <div class="add-friend-header">
                <h3></h3>
                <button class="close-button" @click="closeAddFriendOverlay()">
                    <svg viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg">
                        <path :d="clearIconPath"></path>
                    </svg>
                </button>
            </div>
            <div class="add-friend-search-container">
                <input type="text" v-model.trim="addFriendSearchValue" placeholder="" class="add-friend-search-input"
                    @keyup.enter="searchNewFriendsByEmail(addFriendSearchValue)" />
                <button @click="searchNewFriendsByEmail(addFriendSearchValue)" :disabled="remoteSearchLoading"></button>
            </div>

            <div class="add-friend-results">
                <p v-if="remoteSearchLoading" class="loading-item">...</p>
                <p v-else-if="remoteSearchError" class="error-message">{{ remoteSearchError }}</p>
                <p v-else-if="addFriendSearchValue.length === 0 && searchedNewUser === null" class="empty-list">
                    
                </p>
                <p v-else-if="searchedNewUser === null" class="empty-list"></p>
                <div v-else-if="searchedNewUser" class="add-friend-user-card">
                    <img :src="searchedNewUser.avatar" class="avatar"
                        :alt="searchedNewUser.name + ''" />
                    <div class="user-info">
                        <span class="name">{{ searchedNewUser.nickname || searchedNewUser.name }}</span>
                        <span class="email">{{ searchedNewUser.email }}</span>
                    </div>
                    <button class="add-button-action" @click="addNewFriend(searchedNewUser)"></button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import svg from '@/assets/svg/svg';

export default {
    name: 'UserListHeader',
    emits: ['select-user', 'friend-added'],
    props: {
        friends: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            searchValue: '',
            isFocused: false,
            searchIconPath: svg.userList_search,
            clearIconPath: svg.userList_clear,
            addIconPath: svg.userList_add,

            showAddFriendOverlay: false,
            addFriendSearchValue: '',

            remoteSearchLoading: false,
            searchedNewUser: null,
            remoteSearchError: null,

            highlightFirstFriend: false, // 
            highlightNewUserItem: false // 
        };
    },
    computed: {
        isValidEmail() {
            return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.searchValue);
        },
        filteredFriends() {
            if (!this.searchValue) {
                return [];
            }
            const searchTerm = this.searchValue.toLowerCase();
            return this.friends.filter(user => {
                return (
                    (user.email && user.email.toLowerCase().includes(searchTerm)) ||
                    (user.name && user.name.toLowerCase().includes(searchTerm)) ||
                    (user.nickname && user.nickname.toLowerCase().includes(searchTerm))
                );
            });
        },
        showSearchResultsOverlay() {
            // 
            return (this.searchValue.length > 0 && this.isFocused) || this.searchedNewUser !== null || this.remoteSearchError !== null || this.filteredFriends.length > 0;
        }
    },
    methods: {
        handleFocus() {
            this.isFocused = true;
            this.highlightFirstFriend = this.filteredFriends.length > 0; // 
            this.highlightNewUserItem = this.searchedNewUser !== null && !this.remoteSearchLoading && !this.remoteSearchError; // 
        },
        handleBlur() {
            //  blur  setTimeout  isFocused 
            //  relatedTarget
            //  overlay isFocused
            //  overlay  isFocused

            // console.log("Blur event fired. Related Target:", event.relatedTarget);

            //  nextTick  DOM 
            this.$nextTick(() => {
                const resultsOverlay = this.$el.querySelector('.search-results-overlay');
                if (resultsOverlay && resultsOverlay.contains(document.activeElement)) {
                    //  overlay  isFocused  true
                    return;
                }
                // 
                this.isFocused = false;
                if (!this.searchValue && !this.searchedNewUser && !this.remoteSearchError) {
                    this.resetRemoteSearchState();
                }
                this.highlightFirstFriend = false; // 
                this.highlightNewUserItem = false; // 
            });
        },
        clearSearchInput() {
            this.searchValue = '';
            this.resetRemoteSearchState();
            this.highlightFirstFriend = false;
            this.highlightNewUserItem = false;
            this.$nextTick(() => {
                if (this.$refs.searchInput) {
                    this.$refs.searchInput.focus();
                }
            });
        },
        handleSearchSubmit() {
            // 
            if (this.filteredFriends.length > 0) {
                this.selectUserFromSearch(this.filteredFriends[0]);
            }
            // 
            else if (this.isValidEmail) {
                this.searchNewFriendsByEmail(this.searchValue);
                // 
                if (this.searchedNewUser && !this.remoteSearchLoading && !this.remoteSearchError) {
                    this.addNewFriend(this.searchedNewUser);
                }
            }
            // 
            else if (this.searchedNewUser && !this.remoteSearchLoading && !this.remoteSearchError) {
                this.addNewFriend(this.searchedNewUser);
            }
            // 
            else {
                console.log('UserListHeader: Enter pressed, but no valid action to perform.');
            }
        },

        selectUserFromSearch(user) {
            this.$emit('select-user', user);
            this.searchValue = '';
            this.isFocused = false; //  false
            this.resetRemoteSearchState();
            this.highlightFirstFriend = false; // 
            this.highlightNewUserItem = false; // 
        },

        openAddFriendOverlay() {
            this.showAddFriendOverlay = true;
            this.addFriendSearchValue = '';
            this.resetRemoteSearchState();
        },
        closeAddFriendOverlay() {
            this.showAddFriendOverlay = false;
            this.addFriendSearchValue = '';
            this.resetRemoteSearchState();
        },

        searchNewFriendsByEmail(emailToSearch) {
            const email = emailToSearch.trim();
            this.highlightFirstFriend = false; // 
            this.highlightNewUserItem = false; // 

            if (!email) {
                this.remoteSearchError = '';
                this.searchedNewUser = null;
                return;
            }

            if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
                this.remoteSearchError = '';
                this.searchedNewUser = null;
                return;
            }

            // friends
            const isFriend = this.friends.some(friend => friend.email && friend.email.toLowerCase() === email.toLowerCase());
            if (isFriend) {
                this.remoteSearchError = '';
                this.searchedNewUser = null;
                return;
            }

            this.remoteSearchLoading = true;
            this.remoteSearchError = null;
            this.searchedNewUser = null;

            this.$axios.get(`/user/search-by-email`, {
                params: { email: email },
                withCredentials: true
            })
            .then(response => {
                if (response.data.success && response.data.user) {
                    this.searchedNewUser = response.data.user;
                    // 
                    if (this.isFocused) {
                        this.highlightNewUserItem = true;
                    }
                } else {
                    this.remoteSearchError = response.data.message || '';
                    this.searchedNewUser = null;
                }
            })
            .catch(error => {
                console.error('UserListHeader: Remote search failed:', error);
                this.remoteSearchError = '';
                this.searchedNewUser = null;
            })
            .finally(() => {
                this.remoteSearchLoading = false;
            });
        },

        addNewFriend(user) {
            this.$axios.post('/user/add-friend', { target_user_id: user.user_id }, { withCredentials: true })
                .then(response => {
                    if (response.data.success) {
                        alert(` ${user.nickname || user.name} `);
                        this.$emit('friend-added');
                        this.closeAddFriendOverlay();
                        this.searchValue = '';
                        this.isFocused = false; //  false
                        this.resetRemoteSearchState();
                        this.highlightFirstFriend = false; // 
                        this.highlightNewUserItem = false; // 
                    } else {
                        alert(`: ${response.data.message || ''}`);
                    }
                })
                .catch(error => {
                    console.error('UserListHeader: Add friend request failed:', error);
                    alert('');
                });
        },
        resetRemoteSearchState() {
            this.searchedNewUser = null;
            this.remoteSearchError = null;
            this.remoteSearchLoading = false;
        }
    },
    watch: {
        searchValue(newValue, oldValue) {
            if (this.isFocused) { //  searchValue 
                if (this.isValidEmail) {
                     this.searchNewFriendsByEmail(newValue);
                } else if (!newValue) {
                    this.resetRemoteSearchState();
                }
                //  filteredFriends
                this.highlightFirstFriend = this.filteredFriends.length > 0;
            } else if (!newValue) {
                 //  searchValue 
                this.resetRemoteSearchState();
            }
            // 
            if (!this.isFocused) {
                this.highlightFirstFriend = false;
                this.highlightNewUserItem = false;
            }
        },
        filteredFriends: {
            handler(newVal) {
                //  filteredFriends  highlightFirstFriend
                if (this.isFocused) {
                    this.highlightFirstFriend = newVal.length > 0;
                }
            },
            deep: true
        },
        searchedNewUser: {
            handler(newVal) {
                //  searchedNewUser  highlightNewUserItem
                if (this.isFocused) {
                    this.highlightNewUserItem = newVal !== null && !this.remoteSearchLoading && !this.remoteSearchError;
                }
            }
        },
        isFocused(newVal) {
            if (!newVal) {
                // 
                this.highlightFirstFriend = false;
                this.highlightNewUserItem = false;
                // 
                if (!this.searchValue && !this.searchedNewUser && !this.remoteSearchError) {
                    this.resetRemoteSearchState();
                }
            } else {
                // 
                this.highlightFirstFriend = this.filteredFriends.length > 0;
                this.highlightNewUserItem = this.searchedNewUser !== null && !this.remoteSearchLoading && !this.remoteSearchError;
            }
        }
    }
};
</script>

