<template>
    <div class="login-container">
        <!--  -->
        <div class="copy_nav_status"></div>

        <!--  -->
        <form id="form" autocapitalize="off" autocomplete="off" @submit.prevent="login">
            <div class="dl-zc-title"></div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="1">
                <input v-model="email" type="email" name="email" class="dl-zc-input" maxlength="100"
                    placeholder="" />
                <Clear @click="clear_email"></Clear>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="2">
                <input v-model="password" type="password" name="password" class="dl-zc-input" maxlength="100"
                    placeholder="" />
                <Clear @click="clear_password"></Clear>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="3">
                <div class="dl-btn" id="login-btn" @click="login"></div>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="4">
                <div class="dl-account-nformation">
                    <span @click="free_register"></span>
                    <span @click="forgotPassword"></span>
                </div>
            </div>
        </form>

        <div class="verification-modal-overlay" v-if="showVerificationModal">
            <div class="verification-modal-content">
                <h3></h3>
                <p> **{{ email }}** 6</p>
                <input type="text" v-model="userVerificationCode" maxlength="6" placeholder="6" />
                <div class="modal-buttons">
                    <button @click="verifyCodeFromModal"></button>
                    <button @click="cancelVerification"></button>
                </div>
            </div>
        </div>

        <div>{{ err }}</div>
    </div>
</template>

<script>
// login.vue

export default {
    name: "Login",
    data() {
        return {
            err: '',
            email: "test1@test1.com", // 
            password: "111111", // 
            showVerificationModal: false, // 
            userVerificationCode: "" // 
        };
    },
    methods: {
        // 
        clear_email() {
            this.email = "";
        },
        // 
        clear_password() {
            this.password = "";
        },
        // 
        free_register() {
            this.$router.push("/Register");
        },
        // 
        async login() {
            try {
                //  this.$axios  POST 
                const response = await this.$axios.post(
                    "/login", //  baseURL
                    { email: this.email, password: this.password },
                    { headers: { 'Content-Type': 'application/json' } }
                );

                if (response.data.success) {
                    //   login 
                    //  dispatch  Vuex store

                    // console.log('login======', response.data.user)
                    this.$store.dispatch('loginSuccess', response.data.user);

                    this.$store.dispatch('error/addError', {
                        msg: '',
                        type: 'success'
                    });

                    //  /Home
                    // 
                    this.$router.push("/Home");

                } else {
                    // 
                    let errorMessage = '';
                    if (response.data.message) {
                        errorMessage = response.data.message;
                    } else if (response.status === 410) {
                        errorMessage = '';
                    } else if (response.status === 411) {
                        errorMessage = '';
                    }
                    this.$store.dispatch('error/addError', {
                        msg: errorMessage,
                        type: 'error'
                    });
                    console.log(errorMessage);
                }
            } catch (error) {
                // 
                const errorMessage = error.response?.data?.message || '';
                this.$store.dispatch('error/addError', {
                    msg: errorMessage,
                    type: 'error'
                });
                this.err = error
                console.error(errorMessage);
            }
        },
        // 
        async forgotPassword() {
            if (!this.email) {
                this.$store.dispatch('error/addError', {
                    msg: '',
                    type: 'error'
                });
                return;
            }

            try {
                // 1. 
                const sendCodeResponse = await this.$axios.post("/send-code-for-forgot-password",
                    { email: this.email },
                    { withCredentials: true }
                );

                if (!sendCodeResponse.data.success) {
                    this.$store.dispatch('error/addError', {
                        msg: sendCodeResponse.data.message || '',
                        type: 'error'
                    });
                    return;
                }

                this.$store.dispatch('error/addError', {
                    msg: '',
                    type: 'success'
                });

                // 2. 
                this.showVerificationModal = true;

            } catch (error) {
                console.error(':', error);
                this.$store.dispatch('error/addError', {
                    msg: error.response?.data?.message || '',
                    type: 'error'
                });
            }
        },
        // 
        async verifyCodeFromModal() {
            if (!this.userVerificationCode) {
                this.$store.dispatch('error/addError', {
                    msg: '',
                    type: 'error'
                });
                return;
            }

            try {
                // 3. 
                const verifyCodeResponse = await axios.post(
                    "/verify-code-for-forgot-password",
                    { email: this.email, code: this.userVerificationCode },
                    { withCredentials: true }
                );

                if (verifyCodeResponse.data.success) {
                    // this.$store.dispatch('error/addError', {
                    //     msg: '...',
                    //     type: 'success'
                    // });
                    this.showVerificationModal = false; // 
                    this.userVerificationCode = ''; // 
                    // 4.  ForgetPassword 
                    this.$router.push({
                        path: "/ForgetPassword",
                        query: { email: this.email }
                    });
                } else {
                    this.$store.dispatch('error/addError', {
                        msg: verifyCodeResponse.data.message || '',
                        type: 'error'
                    });
                }
            } catch (error) {
                console.error(':', error);
                this.$store.dispatch('error/addError', {
                    msg: error.response?.data?.message || '',
                    type: 'error'
                });
            }
        },
        // 
        cancelVerification() {
            this.showVerificationModal = false; // 
            this.userVerificationCode = ''; // 
            this.$store.dispatch('error/addError', {
                msg: '',
                type: 'info'
            });
        }
    }
};
</script>

