<template>
    <div class="ChangePassword-container">
        <Header :header_name="header_name"></Header>

        <div class="content">
            <div class="head"></div>
        </div>

        <!--   SetNewPassword.vue  SetNewPassword.vue -->
        <form @submit.prevent="submitCode" autocapitalize="off" autocomplete="off">
            <div class="code-box">
                <div class="text"></div>
                <input type="text" v-model="codeInput" maxlength="6" name="ver_code" placeholder="" />
                <div class="Re-obtain" @click="countdown === 0 && sendCode()" :class="{ disabled: countdown > 0 }">
                    {{ countdown > 0 ? `${countdown}s` : '' }}
                </div>
            </div>

            <div class="btn_box">
                <button type="submit" class="submit" :disabled="!codeInput || codeInput.length !== 6"></button>
            </div>
        </form>

    </div>
</template>

<script>
export default {
    name: 'ChangePassword',
    data() {
        return {
            header_name: '',
            countdown: 0,
            timer: null,
            codeInput: ''
        };
    },
    computed: {
        userEmail() {
            return this.$store.getters.getUserEmail;
        }
    },
    mounted() {
        console.log(':', this.userEmail);
    },
    methods: {
        // 
        toBack() {
            this.$router.go(-1);
        },
        // 
        async sendCode() {
            if (!this.userEmail) {
                alert('');
                return;
            }

            try {
                const res = await fetch('http://localhost:3081/api/send-code', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    credentials: 'include',
                    body: JSON.stringify({ email: this.userEmail })
                });
                const data = await res.json();
                if (data.success) {
                    console.log('');
                } else {
                    alert(data.message);
                }

                // 60
                this.countdown = 60;
                this.timer = setInterval(() => {
                    this.countdown--;
                    if (this.countdown <= 0) {
                        clearInterval(this.timer);
                    }
                }, 1000);
            } catch (err) {
                console.error('', err);
                console.log('');
            }
        },
        // 
        async submitCode() {
            const code = this.codeInput;

            if (!this.userEmail) {
                console.log('');
                return;
            }

            try {
                const res = await fetch('http://localhost:3081/api/verify-code', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    credentials: 'include',
                    body: JSON.stringify({
                        email: this.userEmail,
                        code
                    })
                });
                const data = await res.json();
                if (data.success) {
                    console.log('');
                    this.$router.push('/SetNewPassword');
                } else {
                    console.log(data.message || '');
                }
            } catch (err) {
                console.error('', err);
                console.log('');
            }
        },
    },
    beforeDestroy() {
        if (this.timer) {
            clearInterval(this.timer);
        }
    },
};
</script>


