<template>
    <div class="friends_container">
        <div class="friends-list" :style="{ width: userListWidth + 'px' }">
            <UserListHeader :friends="allFriends" @select-user="handleSelectUser" @friend-added="handleFriendAdded" />
            <div class="friend-items">
                <template v-if="Object.keys(groupedFriends).length > 0">
                    <div v-for="(group, letter) in groupedFriends" :key="letter">
                        <div class="letter-header">{{ letter }}</div>
                        <div v-for="friend in group" :key="friend.user_id" class="friend-item"
                            :class="{ 'selected': currentChatUser && currentChatUser.user_id === friend.user_id }"
                            @dblclick="handleFriendClick(friend)"> <img :src="friend.avatar" class="avatar" :alt="friend.name" />
                            <span class="name">{{ friend.nickname || friend.name }}</span>
                        </div>
                    </div>
                </template>
                <div v-else class="empty-friends-list">
                    <p></p>
                </div>
            </div>
        </div>

        <div class="divider" @mousedown="startDrag"></div>

        <div class="blank-area">
            <template v-if="currentChatUser">
                <h2> {{ currentChatUser.nickname || currentChatUser.name }} </h2>
                <p v-if="currentChatUser.email">: {{ currentChatUser.email }}</p>
                <img :src="currentChatUser.avatar" alt=""
                    style="width: 100px; height: 100px; border-radius: 50%;" />
            </template>
            <template v-else>
                LiuHu
            </template>
        </div>
    </div>
</template>

<script>
import UserListHeader from '@/components/UserListHeader.vue';
import { pinyin } from 'pinyin-pro';
import { mapActions } from 'vuex'; //   mapActions

export default {
    name: 'Friends',
    components: {
        UserListHeader
    },
    props: ['currentUser'],
    data() {
        return {
            userListWidth: 250,
            dragging: false,
            currentChatUser: null, //  Friends 
            allFriends: []
        };
    },
    computed: {
        groupedFriends() {
            const groups = {};
            const regAlphabetic = /^[A-Z]$/;

            this.allFriends.forEach(friend => {
                const displayName = friend.nickname || friend.name || '';
                let firstChar = '';
                if (displayName.length > 0) {
                    firstChar = displayName.charAt(0).toUpperCase();
                }

                let groupKey = '';
                if (regAlphabetic.test(firstChar)) {
                    groupKey = firstChar;
                } else if (/^[\u4e00-\u9fa5]$/.test(firstChar)) {
                    const pinyinResult = pinyin(displayName, { toneType: 'none', type: 'array' });
                    groupKey = pinyinResult.length > 0 ? pinyinResult[0].charAt(0).toUpperCase() : '#';
                } else {
                    groupKey = '#';
                }

                if (!groups[groupKey]) {
                    groups[groupKey] = [];
                }
                groups[groupKey].push(friend);
            });

            for (const key in groups) {
                groups[key].sort((a, b) => {
                    const nameA = (a.nickname || a.name || '');
                    const nameB = (b.nickname || b.name || '');
                    if (/^[\u4e00-\u9fa5]/.test(nameA) && /^[\u4e00-\u9fa5]/.test(nameB)) {
                        return pinyin(nameA, { toneType: 'none', type: 'array' }).join('').localeCompare(
                            pinyin(nameB, { toneType: 'none', type: 'array' }).join('')
                        );
                    }
                    return nameA.localeCompare(nameB);
                });
            }

            const sortedKeys = Object.keys(groups).sort((a, b) => {
                if (a === '#') return 1;
                if (b === '#') return -1;
                return a.localeCompare(b);
            });

            const sortedGroups = {};
            sortedKeys.forEach(key => {
                sortedGroups[key] = groups[key];
            });

            return sortedGroups;
        }
    },
    methods: {
        ...mapActions(['selectChatUser']), //   Vuex action
        handleSelectUser(user) {
            //  UserListHeader  @select-user
            //  UserListHeader 
            console.log('Friends  (UserListHeader ):', user);
            this.currentChatUser = user;
        },
        //  
        handleFriendClick(friend) {
            // 1.  Friends 
            this.currentChatUser = friend;
            console.log('Friends :', friend);

            // 2.  Vuex 
            this.selectChatUser(friend); //   Vuex action

            // 3.  Home 
            //  Home  'Home'  '/'
            //  /home this.$router.push('/home');
            //  { name: 'Home' }
            this.$router.push('/Home'); //  
        },
        handleFriendAdded() {
            console.log('Friends  friend-added ...');
            this.fetchFriends();
        },
        fetchFriends() {
            this.$axios.get('/user/friends', { withCredentials: true })
                .then(response => {
                    if (response.data.success && Array.isArray(response.data.friends)) {
                        this.allFriends = response.data.friends;
                    } else {
                        console.error(':', response.data.message || '');
                        this.allFriends = [];
                    }
                })
                .catch(error => {
                    console.error(':', error);
                    this.allFriends = [];
                    this.currentChatUser = null;
                });
        },
        startDrag(e) {
            e.preventDefault();
            this.dragging = true;
            document.addEventListener('mousemove', this.onDragging);
            document.addEventListener('mouseup', this.stopDrag);
        },
        onDragging(e) {
            if (this.dragging) {
                const home = document.querySelector('.friends_container');
                if (!home) {
                    console.error('.friends_container not found!');
                    return;
                }
                const rect = home.getBoundingClientRect();
                const offsetX = e.clientX - rect.left;
                const minWidth = 200;
                const maxWidth = 300;
                const newWidth = Math.min(Math.max(offsetX, minWidth), maxWidth);
                this.userListWidth = newWidth;
            }
        },
        stopDrag() {
            this.dragging = false;
            document.removeEventListener('mousemove', this.onDragging);
            document.removeEventListener('mouseup', this.stopDrag);
            localStorage.setItem('userListWidth', this.userListWidth);
        }
    },
    mounted() {
        const savedWidth = localStorage.getItem('userListWidth');
        if (savedWidth) {
            this.userListWidth = parseInt(savedWidth);
        }
        this.fetchFriends();
    },
};
</script>

