<template>
    <div class="register-container" autocapitalize="off" autocomplete="off">
        <!--  -->
        <div class="copy_nav_status"></div>

        <!--  -->
        <form id="form" autocapitalize="off" autocomplete="off" @submit.prevent="register">
            <div class="dl-zc-title">
                <div class="back_login" @click="back_login_fn">
                    <div class="back_login_inner">
                        <van-icon name="arrow-left" />
                    </div>
                </div>
                <div></div>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="1">
                <input v-model="email" type="email" name="email" class="dl-zc-input" maxlength="30"
                    placeholder="" />
                <Clear @click="clear_email"></Clear>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="2">
                <input v-model="password" type="password" name="password" class="dl-zc-input" maxlength="30"
                    placeholder="" />
                <Clear @click="clear_password"></Clear>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="3">
                <input v-model="confirmPassword" type="password" name="confirmPassword" class="dl-zc-input"
                    maxlength="30" placeholder="" />
                <Clear @click="clear_confirmPassword"></Clear>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="4">
                <input v-model="verificationCode" type="text" name="verificationCode" class="dl-zc-input" maxlength="6"
                    placeholder="" />
                <div class="ver-code" id="getCodeBtn" @click="getVerificationCode" :class="{ disabled: codeSending }">{{
                    codeSending ? countdown + "" : "" }}</div>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="5">
                <button class="zc-btn" id="register-btn" type="submit" :disabled="!canRegister"
                    @click.prevent="register"></button>
            </div>
            <!--  -->
            <div class="dl-zc-field" data-dl-zc="6">
                <input type="checkbox" v-model="agreementChecked" name="checkbox" />
                <div class="zc-Agreement">
                    
                    <span class="xy" style="margin-left: 4px;"></span>
                    <span class="xy"></span>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
// register.vue
import axios from "axios"; // axios

export default {
    name: "Register",
    data() {
        return {
            email: "", // 
            password: "", // 
            confirmPassword: "", // 

            verificationCode: "",
            agreementChecked: false,
            codeSending: false,
            countdown: 0,
            countdownTimer: null
        };
    },
    computed: {
        canRegister() {
            return (
                this.email &&
                this.password &&
                this.confirmPassword &&
                this.verificationCode &&
                this.password === this.confirmPassword &&
                this.agreementChecked
            );
        }
    },
    methods: {
        // 
        clear_email() {
            this.email = "";
            // console.log("");
        },
        // 
        clear_password() {
            this.password = "";
        },
        // 
        clear_confirmPassword() {
            this.confirmPassword = "";
        },
        // 
        back_login_fn() {
            this.$router.go(-1)
        },
        async getVerificationCode() {
            if (this.codeSending) return;
            if (!this.email) {
                console.log("");
                return;
            }
            try {
                this.codeSending = true;
                this.countdown = 60; // 60
                this.startCountdown();

                const response = await axios.post(
                    "http://localhost:3081/api/send-code",
                    { email: this.email },
                    { withCredentials: true }
                );

                console.log(response.data.message || "");
            } catch (error) {
                console.log(
                    "" +
                    (error.response?.data?.message || error.message)
                );
                this.codeSending = false;
                clearInterval(this.countdownTimer);
            }
        },
        startCountdown() {
            this.countdownTimer = setInterval(() => {
                if (this.countdown > 0) {
                    this.countdown--;
                } else {
                    clearInterval(this.countdownTimer);
                    this.codeSending = false;
                }
            }, 1000);
        },
        async register() {
            if (!this.canRegister) {
                console.log("");
                return;
            }
            try {
                // 
                let verifyRes = await axios.post(
                    "http://localhost:3081/api/verify-code",
                    {
                        email: this.email,
                        code: this.verificationCode
                    },
                    { withCredentials: true }
                );

                if (!verifyRes.data.success) {
                    console.log(verifyRes.data.message || "");
                    return;
                }

                // 
                let registerRes = await axios.post(
                    "http://localhost:3081/api/register",
                    {
                        email: this.email,
                        password: this.password
                    },
                    { withCredentials: true }
                );

                if (registerRes.data.success) {
                    console.log("");
                    // 
                    this.$router.push({ name: "Login" });
                } else {
                    console.log(registerRes.data.message || "");
                }
            } catch (error) {
                console.log(
                    "" +
                    (error.response?.data?.message || error.message)
                );
            }
        }
    },
    beforeDestroy() {
        clearInterval(this.countdownTimer);
    }
};
</script>

