// src/store/index.js
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

const storeAxios = axios.create({
    baseURL: process.env.VUE_APP_API_BASE_URL,
    timeout: 5000,
    withCredentials: true,
    headers: { 'Content-Type': 'application/json', },
});

//  
import error from '@/store/error.js'

//  Vuex 
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        isLoggedIn: false,
        currentUser: null, //  id  user_id
        //  
        currentChatTargetUser: null
    },

    mutations: {
        setLoginStatus(state, status) {
            state.isLoggedIn = status;
        },
        //  setCurrentUser mutation
        setCurrentUser(state, user) {
            state.currentUser = user;
            // console.log(' setCurrentUser mutation', state.currentUser)
        },
        //  logout mutation currentUser
        logout(state) {
            state.isLoggedIn = false;
            state.currentUser = null; // 
            state.currentChatTargetUser = null; //  
        },
        //  currentUser  avatar 
        setCurrentUserAvatar(state, avatarPath) {
            if (state.currentUser) {
                state.currentUser.avatar = avatarPath;
                // console.log('Vuexavatar:', state.currentUser.avatar);
            }
        },
        //  currentUser  gender 
        setCurrentUserGender(state, gender) {
            if (state.currentUser) {
                state.currentUser.gender = gender;
                // console.log('Vuex:', state.currentUser.gender);
            }
        },
        //   mutation 
        setChatTargetUser(state, user) {
            state.currentChatTargetUser = user;
            // console.log('Vuex setChatTargetUser:', user); // 
        }
    },

    actions: {
        async checkLogin({ commit }) {
            try {
                const res = await storeAxios.get('/check-session', { withCredentials: true });

                commit('setLoginStatus', res.data.loggedIn);

                if (res.data.loggedIn && res.data.user) {
                    //  check-session  user 
                    commit('setCurrentUser', res.data.user);
                    // console.log('Vuex (check-session  user ):', res.data.user.avatar);
                } else {
                    commit('setCurrentUser', null); //  user 
                }
            } catch {
                commit('setLoginStatus', false);
                commit('setCurrentUser', null); // 
            }
        },

        //  action 
        //  action  login.vue 
        async loginSuccess({ commit, dispatch }, userData) {
            commit('setLoginStatus', true);
            commit('setCurrentUser', userData); //  profile 
            // console.log('Vuex(loginSuccess):', userData);
            //  action
            // await dispatch('fetchUserProfile'); // 
        },

        logout({ commit }) {
            //  /api/exit 
            // 
            // await axios.post('http://localhost:3081/api/exit', {}, { withCredentials: true });
            commit('setLoginStatus', false);
            commit('setCurrentUser', null);
        },
        //   action 
        selectChatUser({ commit }, user) {
            commit('setChatTargetUser', user);
        }
    },

    getters: {
        isLoggedIn(state) {
            return state.isLoggedIn;
        },
        // getter  currentUser 
        currentUser(state) {
            return state.currentUser;
        },
        //  ID ( profile  id)
        currentUserId(state) {
            return state.currentUser ? state.currentUser.id : null;
        },
        //  user_id ( deactivate-account  user_id)
        currentUserCustomId(state) {
            return state.currentUser ? state.currentUser.user_id : null;
        },
        getUserEmail(state) {
            return state.currentUser?.email || '';
        },
        //   getter 
        currentChatTargetUser(state) {
            return state.currentChatTargetUser;
        }
    },

    modules: {
        error
    },
});