<template>
    <div class="personalInformation-container">
        <Header :header_name="header_name"></Header>

        <ul>
            <li @click="handleAvatarClick('select')">
                <div class="text"></div>
                <div class="user-image">
                    <img :src="displayAvatar" alt="" @click.stop="handleAvatarClick('view')">
                </div>
                <div class="forward_box">
                    <Forward></Forward>
                </div>
                <input ref="avatarInput" type="file" accept="image/*" style="display: none"
                    @change="onAvatarSelected" />
            </li>
            <li @click="$router.push('/UserName')">
                <div class="text"></div>
                <div class="item">{{ currentUser ? currentUser.name : '' }}</div>
                <div class="forward_box">
                    <Forward></Forward>
                </div>
            </li>
            <li @click="$router.push('/UserGender')">
                <div class="text"></div>
                <div class="item">{{ currentUser ? currentUser.gender : '' }}</div>
                <div class="forward_box">
                    <Forward></Forward>
                </div>
            </li>
            <li @click="$router.push('/UserArea')">
                <div class="text"></div>
                <div class="item">{{ currentUser ? currentUser.region : '' }}</div>
                <div class="forward_box">
                    <Forward></Forward>
                </div>
            </li>
            <li @click="$router.push('/UserNickName')">
                <div class="text">h5-APP</div>
                <!--  inckname  h5-APP Friends.vue  -->
                <div class="item">{{ currentUser ? currentUser.nickname : '' }}</div>
                <div class="forward_box">
                    <Forward></Forward>
                </div>
            </li>
        </ul>

        <div v-if="showCropper" class="cropper-modal">
            <div class="cropper-content">
                <cropper ref="cropper" :src="imageToCrop" :stencil-props="{ aspectRatio: 1 / 1 }" class="cropper" />
                <div class="cropper-actions">
                    <button @click="cancelCrop"></button>
                    <button @click="confirmCrop"></button>
                </div>
            </div>
        </div>

        <div v-if="showImageViewer" class="image-viewer-modal" @click="closeImageViewer">
            <img :src="currentImageForViewer" alt="User Avatar" class="full-size-image">
        </div>
    </div>
</template>

<script>
import { Cropper } from 'vue-advanced-cropper';
import 'vue-advanced-cropper/dist/style.css';

import { mapGetters } from 'vuex';

export default {
    name: 'PersonalInformation',
    components: {
        Cropper,
    },
    data() {
        return {
            header_name: '',
            selectedFile: null,
            imageToCrop: null, //  Data URL
            showCropper: false, // 
            showImageViewer: false, // 
        };
    },
    computed: {
        ...mapGetters(['currentUser']),

        displayAvatar() {
            // 1.  Base64  URL
            if (this.imageToCrop && this.showCropper) {
                return this.imageToCrop;
            }

            let finalImageUrl = '';

            // 2.  Vuex 
            if (this.currentUser && this.currentUser.avatar) {
                const avatarPath = this.currentUser.avatar;

                // ****
                if (avatarPath === 'default_user-avator/default.webp') {
                    //  URL
                    finalImageUrl = 'default_user-avator/default.webp';
                }
                // URL
                else if (avatarPath.startsWith('http://') || avatarPath.startsWith('https://')) {
                    finalImageUrl = avatarPath; //  URL
                } else {
                    //  URL
                    const cleanPath = avatarPath.startsWith('/') ? avatarPath.substring(1) : avatarPath;
                    finalImageUrl = `http://localhost:3081/${cleanPath}`;
                }
            }
            // 3.  Vuex 
            else {
                finalImageUrl = 'default_user-avator/default.webp';
            }

            // 4.  Base64 
            if (finalImageUrl && !finalImageUrl.startsWith('data:')) {
                return `${finalImageUrl}?t=${Date.now()}`;
            }

            return finalImageUrl; //  Base64  URL
        },

        currentImageForViewer() {
            //  displayAvatar 
            if (this.imageToCrop && this.showCropper) {
                return this.imageToCrop;
            }

            let finalImageUrl = '';
            if (this.currentUser && this.currentUser.avatar) {
                const avatarPath = this.currentUser.avatar;
                if (avatarPath === 'default_user-avator/default.webp') {
                    finalImageUrl = 'default_user-avator/default.webp';
                } else if (avatarPath.startsWith('http://') || avatarPath.startsWith('https://')) {
                    finalImageUrl = avatarPath;
                } else {
                    const cleanPath = avatarPath.startsWith('/') ? avatarPath.substring(1) : avatarPath;
                    finalImageUrl = `http://localhost:3081/${cleanPath}`;
                }
            } else {
                finalImageUrl = 'default_user-avator/default.webp';
            }
            return finalImageUrl;
        }
    },
    methods: {
        // 
        handleAvatarClick(action) {
            if (action === 'select') {
                this.$refs.avatarInput.click();
            } else if (action === 'view') {
                if (this.currentUser && this.currentUser.avatar) {
                    this.showImageViewer = true;
                }
            }
        },

        // 
        onAvatarSelected(event) {
            const file = event.target.files[0];
            if (file) {
                this.selectedFile = file;
                const reader = new FileReader();
                reader.onload = (e) => {
                    this.imageToCrop = e.target.result;
                    this.showCropper = true;
                };
                reader.readAsDataURL(file);
            }
            event.target.value = '';
        },

        // 
        confirmCrop() {
            const { canvas } = this.$refs.cropper.getResult();
            if (canvas) {
                canvas.toBlob(blob => {
                    this.uploadAvatar(blob);
                }, 'image/png');
                this.showCropper = false;
                this.imageToCrop = null;
                this.selectedFile = null;
            }
        },

        // 
        cancelCrop() {
            this.showCropper = false;
            this.imageToCrop = null;
            this.selectedFile = null;
        },

        // 
        closeImageViewer() {
            this.showImageViewer = false;
        },

        //  Blob 
        async uploadAvatar(fileBlob) {
            const formData = new FormData();
            formData.append('avatar', fileBlob, 'avatar.png');

            try {
                const res = await this.$axios.post('/user/upload/avatar', formData,
                    { headers: { 'Content-Type': 'multipart/form-data' } }
                );

                //   `avatarUrl` `avatar`
                if (res.data.success && res.data.avatarUrl) {
                    this.$store.commit('setCurrentUserAvatar', res.data.avatarUrl); // <--  avatarUrl
                    this.selectedFile = null;

                    if (this.$toast) {
                        console.log('==========', res.data.avatarUrl); // <-- 
                        this.$toast.success('');
                    } else {
                        alert('');
                    }
                } else {
                    //  success  false 
                    if (!res.data.success) {
                        console.error(':', res.data.message);
                        this.$toast.fail(res.data.message || '');
                    } else {
                        //  success  true  avatarUrl 
                        console.warn('URL:', res.data.message);
                        this.$toast.fail('');
                    }
                }
            } catch (error) {
                console.error('', error);
                if (this.$toast) {
                    this.$toast.fail('');
                } else {
                    alert('');
                }
                this.selectedFile = null;
            }
        },
    },
};
</script>

