<template>
    <div class="NewPassword-container">
        <Header :header_name="header_name"></Header>
        <div class="head"></div>
        <form @submit.prevent="submitNewPassword" autocapitalize="off" autocomplete="off">
            <div class="content">
                <div class="content-item">
                    <div class="text"></div>
                    <input type="password" v-model="newPassword" name="new_password" placeholder="" required>
                </div>
            </div>

            <div class="content">
                <div class="content-item">
                    <div class="text"></div>
                    <input type="password" v-model="confirmPassword" name="confirm_password" placeholder="" required>
                </div>
            </div>
            <div class="btn_box">
                <button type="submit" class="submit"
                    :disabled="!newPassword || !confirmPassword || newPassword !== confirmPassword"></button>
            </div>
        </form>
    </div>
</template>

<script>
// NewPassword.vue
export default {
    name: 'NewPassword',
    data() {
        return {
            header_name: '',
            newPassword: '',
            confirmPassword: ''
        };
    },
    computed: {
        userEmail() {
            return this.$store.getters.getUserEmail;
        }
    },
    methods: {
        toBack() {
            this.$router.go(-1);
        },
        async submitNewPassword() {
            if (!this.userEmail) {
                alert('');
                return;
            }

            if (this.newPassword !== this.confirmPassword) {
                alert('');
                return;
            }

            try {
                const res = await fetch('http://localhost:3081/api/set-new-password', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    credentials: 'include',
                    body: JSON.stringify({
                        email: this.userEmail,
                        newPassword: this.newPassword
                    })
                });

                const data = await res.json();

                if (data.success) {
                    //  
                    console.log('...');
                    
                    // 
                    await fetch('http://localhost:3081/api/logout', {
                        method: 'POST',
                        credentials: 'include'
                    });

                    //  Vuex 
                    this.$store.commit('logout');

                    // 
                    this.$router.replace('/Login');
                } else {
                    alert(data.message || '');
                }
            } catch (err) {
                console.error('', err);
                alert('');
            }
        }
    }
};
</script>

