<template>
    <div class="UserGender_container">
        <Header :header_name="header_name" @save="saveGender"></Header>

        <ul class="select_gender_box">
            <li class="gender_li" @click="selectGender('')">
                <div class="li_text"></div>
                <div class="li_icon">
                    <van-icon v-if="selectedGender === ''" color="dodgerblue" size="21" name="success" />
                </div>
            </li>
            <li class="gender_li" @click="selectGender('')">
                <div class="li_text"></div>
                <div class="li_icon">
                    <van-icon v-if="selectedGender === ''" color="dodgerblue" size="21" name="success" />
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'; //  mapGetters

export default {
    name: 'UserGender',
    data() {
        return {
            header_name: '',
            selectedGender: '', // 
            //  Vuex  currentUser
        };
    },
    computed: {
        ...mapGetters(['currentUser']) //  Vuex  currentUser getter
    },
    methods: {
        // 
        selectGender(gender) {
            this.selectedGender = gender;
            // console.log(':', this.selectedGender);
        },
        // 
        saveGender() {
            // 
            if (!this.selectedGender) {
                this.$toast.fail('');
                return;
            }

            // 
            // http://localhost:3081/api/user/profile/update
            this.$axios.post('/user/profile/update', {
                gender: this.selectedGender // 
            }, { withCredentials: true }) // withCredentials  session cookie
                .then(response => {
                    if (response.data.success) { //  success 
                        this.$toast.success('');

                        //  Vuexmutation updateCurrentUser
                        //   Vuex  currentUser.gender
                        //  Vuex store  setGenderMutation mutation
                        this.$store.commit('setCurrentUserGender', this.selectedGender); //  updateCurrentUser mutation

                        this.$router.go(-1); // 
                    } else {
                        // 
                        this.$toast.fail(response.data.message || '');
                        console.error(':', response.data.message);
                    }
                })
                .catch(error => {
                    console.error(':', error);
                    this.$toast.fail('');
                });
        },
        // ()
    },
    created() {
        //  Vuex 
        if (this.currentUser && this.currentUser.gender) {
            this.selectedGender = this.currentUser.gender;
        }
        //  Vuex store 
        //  fetchUserProfile
        //  currentUser  fetchUserProfile
    }
};
</script>

<style scoped lang='less'>
.UserGender_container {
    width: 100%;
    height: 100%;
}

.select_gender_box {
    width: 100%;
    height: fit-content;
    background-color: @255_255_255;
}

.gender_li {
    padding: 12px 12px 0px 12px;
    display: flex;
    align-self: center;
    justify-content: space-between;
}

.li_text {
    font-size: @fontSize14;
    padding-bottom: 10px;
    border-bottom: 1px solid @borderColor;
}
</style>