<template>
    <div class="home_container">
        <div class="user-list" :style="{ width: userListWidth + 'px' }">
           <UserList :currentUser="currentChatUser" @select-user="handleSelectUserFromUserList" />
        </div>

        <div class="divider" @mousedown="startDrag"></div>

        <div class="chat-area">
            <ChatArea :user="currentChatUser" />
        </div>
    </div>
</template>

<script>
import UserList from '@/view/UserList.vue';
import ChatArea from '@/view/ChatArea.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'Home',
    components: { UserList, ChatArea },
    data() {
        return {
            userListWidth: 250,
            dragging: false,
            currentChatUser: null // Home  Vuex  UserList 
        };
    },
    computed: {
        ...mapGetters(['currentChatTargetUser', 'currentUserCustomId']) //  Vuex getter
    },
    watch: {
        //  Vuex 
        currentChatTargetUser(newVal, oldVal) {
            // console.log('Home: currentChatTargetUser changed from Vuex', oldVal, '->', newVal); // Debugging
            if (newVal && (!oldVal || newVal.user_id !== oldVal.user_id)) {
                this.currentChatUser = newVal; //  Home 
                // console.log('Home: currentChatUser updated from Vuex target:', this.currentChatUser); // Debugging
            } else if (!newVal) {
                this.currentChatUser = null; // 
            }
        }
    },
    methods: {
        ...mapActions(['selectChatUser']), //  selectChatUser action

        //  UserList  select-user  ( Home )
        handleSelectUserFromUserList(user) {
            // console.log('Home: User selected from UserList:', user); // Debugging
            this.currentChatUser = user;
            this.selectChatUser(user); //  Vuex 
            //  Home 
            localStorage.setItem('lastSelectedChatUser', user ? user.user_id : ''); //   localStorage
        },
        startDrag(e) {
            e.preventDefault();
            this.dragging = true;
            document.addEventListener('mousemove', this.onDragging);
            document.addEventListener('mouseup', this.stopDrag);
        },
        onDragging(e) {
            if (this.dragging) {
                const home = document.querySelector('.home_container');
                if (!home) {
                    console.error('.home_container not found!');
                    return;
                }
                const rect = home.getBoundingClientRect();
                const offsetX = e.clientX - rect.left;
                const minWidth = 200;
                const maxWidth = 300;
                const newWidth = Math.min(Math.max(offsetX, minWidth), maxWidth);
                this.userListWidth = newWidth;
            }
        },
        stopDrag() {
            this.dragging = false;
            document.removeEventListener('mousemove', this.onDragging);
            document.removeEventListener('mouseup', this.stopDrag);
            localStorage.setItem('userListWidth', this.userListWidth);
        }
    },
    mounted() {
        const savedWidth = localStorage.getItem('userListWidth');
        if (savedWidth) {
            this.userListWidth = parseInt(savedWidth);
        }

        //   mounted  Vuex 
        if (this.currentChatTargetUser) {
            this.currentChatUser = this.currentChatTargetUser;
            // console.log('Home: Mounted, setting currentChatUser from Vuex target:', this.currentChatUser); // Debugging
        } else {
            //  Vuex  localStorage 
            const lastSelectedUserId = localStorage.getItem('lastSelectedChatUser');
            if (lastSelectedUserId) {
                //  UserList Home  ID 
                //  this.currentChatUser UserList  internal logic  select-user
                // Home  lastSelectedUserId  currentChatUser
                //  UserList  localStorage emit  Home  Vuex 
            }
        }
    }
};
</script>

