<template>
    <div class="ChatArea_container" @dragover.prevent @drop="handleDrop" @dragleave="handleDragLeave"
        @dragenter="handleDragEnter">

        <div v-if="isDraggingOver" class="drag-overlay">
            <p></p>
        </div>

        <div class="chat-header" v-if="user">
             {{ user.name }} 
        </div>


        <div class="chat-messages" v-if="user">
            <div v-if="messages.length === 0" class="no-messages"></div>
            <div v-for="msg in messages" :key="msg.dbId ? 'db_' + msg.dbId : 'optimistic_' + msg.id"
                :class="['message-item', msg.senderId === currentLoginUser.user_id ? 'sent' : 'received']">

                <div v-if="msg.senderId !== currentLoginUser.user_id" class="avatar-wrapper">
                    <img :src="getAvatarUrl(user.avatar)" alt="" class="message-avatar" />
                </div>

                <div class="message-bubble">
                    <div class="sender-name">
                        {{ msg.senderId === currentLoginUser.user_id ? (currentLoginUser.nickname ||
                            currentLoginUser.username) : user.name }}
                    </div>

                    <div class="message-content" v-if="isImageMessage(msg.text)">
                        <img :src="getImageUrl(msg.text)" alt="" class="chat-image" @error="handleImageError(msg)"
                            @dblclick="showFullscreen(getImageUrl(msg.text))" />
                        <p v-if="msg.imageLoadError" class="image-error-text"></p>
                    </div>
                    <div class="message-content" v-else>
                        {{ msg.text }}
                    </div>
                </div>

                <div v-if="msg.senderId === currentLoginUser.user_id" class="avatar-wrapper">
                    <img :src="getAvatarUrl(currentLoginUser.avatar)" alt="" class="message-avatar" />
                </div>
            </div>
        </div>

        <div class="resize-handle" @mousedown="startResize"></div>


        <div v-if="isFullscreen" class="fullscreen-overlay" @click="closeFullscreen">
            <button class="close-fullscreen-button" @click.stop="closeFullscreen">&times;</button>
            <img :src="fullscreenImageUrl" alt="" class="fullscreen-image" @click.stop />
        </div>

        <div class="chat-input" v-if="user" :style="{ height: chatInputHeight + 'px' }">
            <textarea v-model="newMessage" @keydown.enter.exact.prevent="sendMessage"
                @keydown.enter.shift.exact.prevent="insertNewline" placeholder="... (Shift+ )"
                class="message-textarea" ref="messageTextarea" @paste="handlePaste" :style="textareaStyle"></textarea>
            <div v-if="imagePreviews.length > 0" class="image-previews">
                <div v-for="(preview, index) in imagePreviews" :key="index" class="image-preview-item">
                    <img :src="preview.src" :alt="preview.name" class="uploaded-image-thumbnail" />
                    <span class="remove-preview" @click="removeImagePreview(index)">&times;</span>
                    <div v-if="preview.uploading" class="upload-progress">...</div>
                    <div v-if="preview.error" class="upload-error">!</div>
                </div>
            </div>
        </div>

        <div v-else class="empty-chat"></div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import io from 'socket.io-client';

export default {
    name: 'ChatArea',
    props: ['user'],
    data() {
        return {
            messages: [],
            newMessage: '',
            socket: null,
            imagePreviews: [], // 
            // 
            isResizing: false,
            chatInputHeight: 190, // chat-input 
            startY: 0, // Y
            startHeight: 0, //  chat-input 
            isDraggingOver: false, // 
            dragLeaveTimeout: null, //  setTimeout ID
            isFullscreen: false, // 
            fullscreenImageUrl: null, //  URL
        };
    },
    computed: {
        ...mapGetters({
            currentLoginUser: 'currentUser'
        }),
        textareaStyle() {
            // 
            const lineHeight = 22; //  CSS .message-textarea  line-height 
            const paddingY = 20; //  (padding-top: 10px + padding-bottom: 10px)
            const borderWidth = 2; //  (border: 1px)

            const currentLines = this.newMessage.split('\n').length;
            const minLines = 1;
            const maxLines = 12;
            const actualLines = Math.max(minLines, Math.min(currentLines, maxLines));

            const calculatedHeight = (actualLines * lineHeight) + paddingY + borderWidth;

            //  textarea  chat-input  chatInputHeight 
            return {
                height: `${calculatedHeight}px`,
                minHeight: `${(minLines * lineHeight) + paddingY + borderWidth}px`,
                maxHeight: `${(maxLines * lineHeight) + paddingY + borderWidth}px`,
                overflowY: actualLines >= maxLines ? 'auto' : 'hidden'
            };
        }
    },
    watch: {
        user(newUser, oldUser) {
            if (newUser && newUser.user_id && newUser.user_id !== (oldUser ? oldUser.user_id : null)) {
                this.messages = [];
                this.loadMessages(newUser.user_id);
                this.newMessage = ''; // 
                this.imagePreviews = []; // 
            } else if (!newUser) {
                this.messages = [];
                this.newMessage = '';
                this.imagePreviews = [];
            }
        },
        currentLoginUser: {
            immediate: true,
            handler(newVal, oldVal) {
                if (newVal && (!oldVal || newVal.user_id !== oldVal.user_id)) {
                    if (this.socket) {
                        this.socket.disconnect();
                    }
                    this.initSocket();
                } else if (!newVal && this.socket) {
                    this.socket.disconnect();
                }
            }
        }
    },
    methods: {

        showFullscreen(imageUrl) {
            this.fullscreenImageUrl = imageUrl;
            this.isFullscreen = true;
            // 
            document.body.style.overflow = 'hidden';
        },

        closeFullscreen() {
            this.fullscreenImageUrl = null;
            this.isFullscreen = false;
            // 
            document.body.style.overflow = 'auto';
        },

        // URL
        getAvatarUrl(relativePath) {
            //  relativePath null
            if (!relativePath) {
                return 'http://localhost:3081/public/default_user-avator/default.webp';
            }

            //  relativePath  URL ( http://...)
            // 
            if (relativePath.startsWith('http://') || relativePath.startsWith('https://')) {
                return relativePath;
            }

            //  relativePath  /uploads/...  public/...
            const baseUrl = 'http://localhost:3081';
            //  '/'  /public/  /uploads/ 
            if (!relativePath.startsWith('/')) {
                relativePath = '/' + relativePath;
            }
            return `${baseUrl}${relativePath}`;
        },
        initSocket() {
            if (!this.currentLoginUser || !this.currentLoginUser.user_id) {
                console.warn('IDSocket.IO');
                return;
            }

            if (this.socket) {
                this.socket.disconnect();
            }

            this.socket = io('http://localhost:3081', {
                withCredentials: true
            });

            this.socket.on('connect', () => {
                // console.log('Socket.IO :', this.socket.id);
                this.socket.emit('authenticate', { user_id: this.currentLoginUser.user_id });
            });

            this.socket.on('chat message', (msg) => {
                // console.log(':', msg);
                if (
                    this.user &&
                    this.currentLoginUser &&
                    (
                        (msg.senderId === this.user.user_id && msg.receiverId === this.currentLoginUser.user_id) ||
                        (msg.senderId === this.currentLoginUser.user_id && msg.receiverId === this.user.user_id)
                    )
                ) {
                    const existingMessageIndex = this.messages.findIndex(m => m.id === msg.id && m.senderId === msg.senderId);
                    if (existingMessageIndex !== -1) {
                        this.$set(this.messages, existingMessageIndex, msg);
                        // console.log(':', msg.id, '->', msg.dbId);
                    } else {
                        this.messages.push(msg);
                        // console.log(':', msg.id, msg.dbId ? '(dbId:' + msg.dbId + ')' : '');
                    }

                    this.scrollChatToBottom();
                }
            });

            this.socket.on('message error', (data) => {
                console.error(':', data.error);
                const index = this.messages.findIndex(m => m.id === data.originalMsgId && m.isOptimistic);
                if (index !== -1) {
                    this.messages.splice(index, 1);
                    // alert(`: ${data.error}`);
                }
            });

            this.socket.on('disconnect', () => {
                console.log('Socket.IO ');
            });

            this.socket.on('connect_error', (err) => {
                console.error('Socket.IO :', err);
            });
        },
        async loadMessages(targetUserCustomId) {
            if (!this.currentLoginUser || !this.currentLoginUser.user_id || !targetUserCustomId) {
                console.warn('ID');
                this.messages = [];
                return;
            }

            try {
                const response = await this.$axios.get(`/user/messages/history/${targetUserCustomId}`, {
                    withCredentials: true
                });

                if (response.data.success) {
                    this.messages = response.data.messages.map(msg => ({
                        ...msg,
                        id: msg.id
                    }));
                    // console.log(':', this.messages.length, '');
                    this.scrollChatToBottom();
                } else {
                    // console.error(':', response.data.message);
                    this.messages = [];
                }
            } catch (error) {
                // console.error(':', error);
                this.messages = [];
            }
        },
        sendMessage() {
            const textContent = this.newMessage.trim();
            const clientMsgIdBase = Date.now();
            let messageCounter = 0;

            if (textContent !== '') {
                const textMessageToSend = {
                    id: clientMsgIdBase + messageCounter++,
                    senderId: this.currentLoginUser.user_id,
                    receiverId: this.user.user_id,
                    text: textContent,
                    timestamp: new Date().toISOString()
                };
                this.socket.emit('chat message', textMessageToSend);
                this.messages.push({ ...textMessageToSend, isOptimistic: true });
            }

            if (this.imagePreviews.length > 0) {
                const imageUrlsToSend = this.imagePreviews
                    .filter(p => p.serverUrl && !p.error && !p.uploading)
                    .map(p => p.serverUrl);

                imageUrlsToSend.forEach(imageUrl => {
                    const imageMessageToSend = {
                        id: clientMsgIdBase + messageCounter++,
                        senderId: this.currentLoginUser.user_id,
                        receiverId: this.user.user_id,
                        text: imageUrl,
                        timestamp: new Date().toISOString()
                    };
                    this.socket.emit('chat message', imageMessageToSend);
                    this.messages.push({ ...imageMessageToSend, isOptimistic: true });
                });
            }

            this.newMessage = '';
            this.imagePreviews.forEach(preview => {
                if (preview.src && preview.src.startsWith('blob:')) {
                    URL.revokeObjectURL(preview.src);
                }
            });
            this.imagePreviews = [];

            this.scrollChatToBottom();
        },
        insertNewline() {
            const textarea = this.$refs.messageTextarea;
            const start = textarea.selectionStart;
            const end = textarea.selectionEnd;
            this.newMessage = this.newMessage.substring(0, start) + '\n' + this.newMessage.substring(end);
            this.$nextTick(() => {
                textarea.selectionStart = textarea.selectionEnd = start + 1;
            });
        },
        isImageMessage(text) {
            return typeof text === 'string' && text.startsWith('/api/upload/chat_image/') && /\.(jpeg|jpg|png|gif|webp)$/i.test(text);
        },
        getImageUrl(text) {
            if (this.isImageMessage(text)) {
                const fullUrl = `http://localhost:3081${text}`;
                return fullUrl;
            }
            return '';
        },
        //  handleImageError UI
        handleImageError(msg) {
            console.error(':', msg.text);
            const index = this.messages.findIndex(m => m.id === msg.id || m.dbId === msg.dbId);
            if (index !== -1) {
                this.$set(this.messages[index], 'imageLoadError', true);
            }
            //  scrollChatToBottom
            // 
        },
        scrollChatToBottom() {
            this.$nextTick(() => {
                const messagesContainer = this.$el.querySelector('.chat-messages');
                if (messagesContainer) {
                    // console.log('scrollHeight =', messagesContainer.scrollHeight, 'clientHeight =', messagesContainer.clientHeight);
                    messagesContainer.scrollTop = messagesContainer.scrollHeight;

                    // 
                    //  DOM 
                    // 
                    const images = messagesContainer.querySelectorAll('.chat-image');
                    let imagesLoaded = 0;
                    const totalImages = images.length;

                    if (totalImages === 0) {
                        // 
                        messagesContainer.scrollTop = messagesContainer.scrollHeight;
                        // console.log('');
                    } else {
                        images.forEach(img => {
                            //  load 
                            if (!img.complete) {
                                img.addEventListener('load', () => {
                                    imagesLoaded++;
                                    if (imagesLoaded === totalImages) {
                                        // 
                                        messagesContainer.scrollTop = messagesContainer.scrollHeight;
                                        // console.log('');
                                    }
                                }, { once: true }); //  once: true 
                                img.addEventListener('error', () => {
                                    // 
                                    imagesLoaded++;
                                    if (imagesLoaded === totalImages) {
                                        messagesContainer.scrollTop = messagesContainer.scrollHeight;
                                        // console.log('');
                                    }
                                }, { once: true });
                            } else {
                                // 
                                imagesLoaded++;
                            }
                        });

                        // 
                        if (imagesLoaded === totalImages) {
                            messagesContainer.scrollTop = messagesContainer.scrollHeight;
                            console.log('');
                        }
                    }

                } else {
                    console.warn(' messagesContainer');
                }
            });
        },

        handleDragEnter(e) {
            e.preventDefault();
            // 
            if (this.dragLeaveTimeout) {
                clearTimeout(this.dragLeaveTimeout);
                this.dragLeaveTimeout = null;
            }
            // 
            this.isDraggingOver = true;
        },

        handleDragOver(e) {
            e.preventDefault();
            // 
            if (this.dragLeaveTimeout) {
                clearTimeout(this.dragLeaveTimeout);
                this.dragLeaveTimeout = null;
            }
            // 
            this.isDraggingOver = true;
        },

        handleDragLeave(e) {
            //  relatedTarget
            //  relatedTarget  null 
            const container = this.$el; //  ChatArea_container 
            if (!e.relatedTarget || !container.contains(e.relatedTarget)) {
                // 
                if (this.dragLeaveTimeout) {
                    clearTimeout(this.dragLeaveTimeout);
                    this.dragLeaveTimeout = null;
                }
                this.isDraggingOver = false;
            } else {
                // 
                // 
                // 
                //  setTimeout 
                //  !container.contains(e.relatedTarget) 
                // 
            }
        },

        async handleDrop(e) {
            e.preventDefault();
            // 
            this.isDraggingOver = false;
            if (this.dragLeaveTimeout) {
                clearTimeout(this.dragLeaveTimeout);
                this.dragLeaveTimeout = null;
            }

            const files = e.dataTransfer.files;
            if (files.length > 0) {
                for (let i = 0; i < files.length; i++) {
                    const file = files[i];
                    if (file.type.startsWith('image/')) {
                        await this.uploadAndPreviewImage(file);
                    } else {
                        alert('');
                    }
                }
            }

            //  
            this.$nextTick(() => {
                if (this.$refs.messageTextarea) {
                    this.$refs.messageTextarea.focus();
                }
            });
        },

        async handlePaste(event) {
            const items = (event.clipboardData || event.originalEvent.clipboardData).items;
            for (const item of items) {
                if (item.type.startsWith('image/')) {
                    const file = item.getAsFile();
                    if (file) {
                        await this.uploadAndPreviewImage(file);
                        event.preventDefault();
                    }
                }
            }
        },
        async uploadAndPreviewImage(file) {
            const localPreviewUrl = URL.createObjectURL(file);
            const previewItem = {
                src: localPreviewUrl,
                name: file.name,
                uploading: true,
                error: false,
                serverUrl: null
            };
            this.imagePreviews.push(previewItem);
            this.scrollChatToBottom();

            const formData = new FormData();
            formData.append('image', file);
            formData.append('receiverId', this.user.user_id);

            try {
                const response = await this.$axios.post('/upload/chat_image', formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                    withCredentials: true
                });

                if (response.data.success && response.data.imageUrl) {
                    previewItem.serverUrl = response.data.imageUrl;
                    previewItem.uploading = false;
                    console.log(':', response.data.imageUrl);
                } else {
                    previewItem.uploading = false;
                    previewItem.error = true;
                    console.error(':', response.data.message);
                }
            } catch (error) {
                previewItem.uploading = false;
                previewItem.error = true;
                console.error(':', error);
            }
        },
        removeImagePreview(index) {
            const previewToRemove = this.imagePreviews[index];
            if (previewToRemove.src && previewToRemove.src.startsWith('blob:')) {
                URL.revokeObjectURL(previewToRemove.src);
            }
            this.imagePreviews.splice(index, 1);
        },

        // ---  ---
        startResize(e) {
            this.isResizing = true;
            this.startY = e.clientY;
            this.startHeight = this.chatInputHeight;

            // 
            document.body.style.userSelect = 'none';
            document.body.style.cursor = 'ns-resize';

            // 
            window.addEventListener('mousemove', this.doResize);
            window.addEventListener('mouseup', this.stopResize);
        },
        doResize(e) {
            if (!this.isResizing) return;

            const deltaY = e.clientY - this.startY;
            let newHeight = this.startHeight - deltaY; // 

            //  190px  280px 
            newHeight = Math.max(190, Math.min(newHeight, 280));

            this.chatInputHeight = newHeight;
        },
        stopResize() {
            this.isResizing = false;
            document.body.style.userSelect = ''; // 
            document.body.style.cursor = ''; // 

            // 
            window.removeEventListener('mousemove', this.doResize);
            window.removeEventListener('mouseup', this.stopResize);
        }
    },
    mounted() {
        if (this.user) {
            this.loadMessages(this.user.user_id);
            //  mounted  scrollChatToBottom
            // 
            setTimeout(() => {
                this.scrollChatToBottom();
            }, 100); // 100
        }
    },
    beforeDestroy() {
        if (this.socket) {
            this.socket.disconnect();
            console.log('ChatArea Socket.IO ');
        }
        this.imagePreviews.forEach(preview => {
            if (preview.src && preview.src.startsWith('blob:')) {
                URL.revokeObjectURL(preview.src);
            }
        });
        // 
        window.removeEventListener('mousemove', this.doResize);
        window.removeEventListener('mouseup', this.stopResize);
    }
};
</script>


