<script>
import { gsap } from "gsap";

export default {
    name: "Index",
    data() {
        return {
            text: "liuhu".split(""), // 
            progress: 0 //  0
        };
    },
    mounted() {
        // 
        this.text.forEach((_, i) => {
            gsap.from(this.$refs["letter" + i], {
                opacity: 0, //  0
                y: 50, //  50px
                delay: i * 0.1, //  0.1 
                duration: 1, //  1 
                ease: "back.out(1.7)"
            });
        });

        //   100 
        gsap.to(this.$data, {
            duration: 3,
            progress: 100,
            ease: "power1.out",
            onComplete: () => {
                this.fadeOutAndRedirect(); // 
            }
        });
    },
    methods: {
        updateProgress(progress) {
            this.progress = progress; // 
        },
        fadeOutAndRedirect() {
            // 
            gsap.to(this.$refs, {
                opacity: 0,
                duration: 1, // 
                onComplete: () => {
                    //  login 
                    this.$router.replace("/Login");
                }
            });
        }
    }
};
</script>

