<script>
import svg from '@/assets/svg/svg.js';
import { mapGetters } from 'vuex';

export default {
    name: 'setting',
    data() {
        return {
            personal_info_d11: svg.personal_info_d11,
            personal_info_d22: svg.personal_info_d22,
            personal_info_d33: svg.personal_info_d33,
            personal_info_d44: svg.personal_info_d44,
            personal_info_d55: svg.personal_info_d55,

            change_password_d11: svg.change_password_d11,

            log_out_d11: svg.log_out_d11,
            log_out_d22: svg.log_out_d22,
        };
    },
    computed: {
        ...mapGetters(['currentUser']),

        displayAvatar() {
            let finalImageUrl = '';

            // 2.  Vuex 
            if (this.currentUser && this.currentUser.avatar) {
                const avatarPath = this.currentUser.avatar;

                // ****
                if (avatarPath === 'default_user-avator/default.webp') {
                    //  URL
                    finalImageUrl = 'default_user-avator/default.webp';
                }
                // URL
                else if (avatarPath.startsWith('http://') || avatarPath.startsWith('https://')) {
                    finalImageUrl = avatarPath; //  URL
                } else {
                    //  URL
                    const cleanPath = avatarPath.startsWith('/') ? avatarPath.substring(1) : avatarPath;
                    finalImageUrl = `http://localhost:3081/${cleanPath}`;
                }
            }
            // 3.  Vuex 
            else {
                finalImageUrl = 'default_user-avator/default.webp';
            }

            // 4.  Base64 
            if (finalImageUrl && !finalImageUrl.startsWith('data:')) {
                return `${finalImageUrl}?t=${Date.now()}`;
            }

            return finalImageUrl; //  Base64  URL
        },
    },
    // ... methods
    created() {
        //  currentUser 
        //  currentUser  avatar  checkLogin 
        if (!this.currentUser) {
            this.refreshUserProfile();
        }
    },
    methods: {
        async refreshUserProfile() {
            try {
                const response = await this.$axios.get('/user/profile');
                if (response.data.success) {
                    this.$store.dispatch('loginSuccess', response.data.user);
                    // console.log("Setting ");
                } else {
                    console.warn("Setting :", response.data.message);
                }
            } catch (error) {
                console.error("Setting :", error);
                //  Vuex 
            }
        }
    },
};
</script>

