<!--  API  selectUser  -->

<template>
    <div class="user-list-wrapper">
        <ul class="UserList_container">
            <UserListHeader :friends="users" :currentUser="currentUser" @select-user="selectUser"
                @friend-added="handleFriendAdded" />

            <div class="user-list">
                <li v-if="loading" class="loading-item default-item">...</li>
                <li v-else-if="users.length === 0" class="empty-list default-item"></li>

                <li v-for="user in users" :key="user.user_id" @click="selectUser(user)"
                    :class="{ active: currentUser && currentUser.user_id === user.user_id, 'default-user-item': true }">
                    <img :src="user.avatar" class="avatar" :alt="user.name + ''" />
                    <span class="name">{{ user.nickname || user.name }}</span>
                </li>
            </div>
        </ul>
    </div>
</template>



<!-- 1 -->
<script>
import UserListHeader from '@/components/UserListHeader.vue';
import { mapGetters } from 'vuex'; //  Vuex  mapGetters ID

export default {
    name: 'UserList',
    components: {
        UserListHeader, // 
    },
    props: {
        // 
        currentUser: {
            type: Object,
            default: null
        }
    },
    data() {
        return {
            users: [], // 
            loading: false, // 
            error: null, // 
            currentSelectedUser: null, // 
        };
    },
    computed: {
        ...mapGetters(['currentUserCustomId']), //  Vuex  user_id
    },
    watch: {
        //  currentUser
        currentUser: {
            immediate: true, // 
            handler(newVal, oldVal) {
                if (newVal && (!this.currentSelectedUser || newVal.user_id !== this.currentSelectedUser.user_id)) {
                    this.currentSelectedUser = newVal; // 
                } else if (!newVal && this.currentSelectedUser) {
                    this.currentSelectedUser = null; //  null
                }
            }
        },
        //  users 
        users: {
            immediate: true, // 
            handler(newUsers, oldUsers) {
                if (newUsers.length > 0 && (newUsers.length !== oldUsers.length || JSON.stringify(newUsers) !== JSON.stringify(oldUsers))) {
                    this.ensureUserSelected(); // 
                } else if (newUsers.length === 0 && this.currentSelectedUser) {
                    this.currentSelectedUser = null; // 
                }
            }
        }
    },
    methods: {
        //  URL
        getAvatarUrl(relativePath) {
            if (!relativePath) {
                return 'http://localhost:3081/public/default_user-avator/default.webp'; // 
            }
            if (relativePath.startsWith('http://') || relativePath.startsWith('https://')) {
                return relativePath; // 
            }
            const baseUrl = 'http://localhost:3081';
            return `${baseUrl}${relativePath.startsWith('/') ? '' : '/'}${relativePath}`;
        },

        // 
        fetchFriends() {
            this.loading = true;
            this.error = null;
            this.$axios.get('/user/friends', { withCredentials: true })
                .then(response => {
                    if (response.data.success && Array.isArray(response.data.friends)) {
                        // 
                        this.users = response.data.friends.filter(friend => friend.user_id !== this.currentUserCustomId);
                    } else {
                        this.error = response.data.message || '';
                    }
                })
                .catch(error => {
                    this.error = '';
                })
                .finally(() => {
                    this.loading = false;
                });
        },

        // 
        selectUser(user) {
            if (!user || (this.currentSelectedUser && this.currentSelectedUser.user_id === user.user_id)) return;
            this.currentSelectedUser = user;
            this.$emit('select-user', user); // 
        },

        // 3
        ensureUserSelected() {
            //  1 currentUser
            if (this.currentUser && this.users.length > 0) {
                const matched = this.users.find(u => u.user_id === this.currentUser.user_id);
                if (matched) {
                    this.selectUser(matched);
                    return;
                }
            }

            //  2 localStorage 
            const lastSelectedUserId = localStorage.getItem('lastSelectedChatUser');
            if (lastSelectedUserId) {
                const matched = this.users.find(user => user.user_id === lastSelectedUserId);
                if (matched) {
                    this.selectUser(matched);
                    return;
                } else {
                    localStorage.removeItem('lastSelectedChatUser'); // 
                }
            }

            //  3
            if (!this.currentSelectedUser && this.users.length > 0) {
                this.selectUser(this.users[0]);
            } else if (this.users.length === 0) {
                this.selectUser(null);
            }
        },

        // 
        handleFriendAdded() {
            this.fetchFriends(); // 
        },
    },
    created() {
        this.fetchFriends(); // 
    },
};
</script>



