<template>
    <div>
        <!-- header.vue -->
        <div class="header">
            <div class="header-back" @click="toBack">
                <Back></Back>
            </div>
            <div class="header-name">{{ header_name }}</div>
            <div class="header-more">
                <div class="save" v-if="isPIPages" @click="$emit('save')"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Header',
    props: {
        header_name: {
            type: String,
            required: true, // 
        }
    },
    computed: {
        //  UserNameUserGender  UserArea  UserAreaDetail 
        // 
        isPIPages() {
            return this.$route.path === '/UserName' ||
                this.$route.path === '/UserGender' ||
                this.$route.path === '/UserAreaDetail' ||
                this.$route.path === '/UserNickName';
        }
    },
    methods: {
        toBack() {
            this.$router.back(); // 
        },
    }
};
</script>

