<template>
    <div class="UserAreaDetail_container">
        <Header :header_name="header_name" @save="handleSave"></Header>

        <div class="user_area_box">
            <div class="title_text"></div>
            <div class="current_location_box">
                <div class="location_icon">
                    <van-icon size="20" name="location" />
                </div>
                <div class="location_text">
                    {{ provinceName }}
                    <span v-if="selectedCity && !isSpecialCity"> {{ selectedCity }}</span>
                    <span v-if="isSpecialCity && selectedDistrict"> {{ selectedDistrict }}</span>
                </div>
            </div>
        </div>

        <div class="select_region_box">
            <div class="select_region_title"></div>
            <ul class="select_region_content">
                <li class="region_li" v-for="(item, index) in (isSpecialCity ? districtList : cityList)" :key="index"
                    @click="handleItemClick(item)">
                    <div class="li">
                        <div class="li_text">{{ item.name }}</div>
                        <div class="li_right">
                            <div class="current_region"
                                v-if="isSpecialCity ? selectedDistrict === item.name : selectedCity === item.name">
                            </div>
                            <div class="li_forward">
                                <Forward></Forward>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

import regionData from '../../../region.json'; // 

export default {
    name: 'UserAreaDetail',
    data() {
        return {
            header_name: '',
            provinceName: '',           // 
            cityList: [],               // 
            districtList: [],           // /
            selectedCity: '',           //  ()
            selectedDistrict: '',       //  (/)
            isSpecialCity: false,       //  ()
        };
    },
    methods: {
        handleSave() {
            let fullRegion = '';
            if (this.isSpecialCity) {
                if (!this.selectedDistrict) {
                    console.warn("");
                    this.$toast.fail('');
                    return;
                }
                fullRegion = `${this.provinceName} ${this.selectedDistrict}`;
            } else {
                if (!this.selectedCity) {
                    console.warn("");
                    this.$toast.fail('');
                    return;
                }
                fullRegion = `${this.provinceName} ${this.selectedCity}`;
            }

            console.log('', fullRegion);

            // http://localhost:3081/api/user/profile/update
            this.$axios.post('/user/profile/update', {
                region: fullRegion
            }, { withCredentials: true })
                .then(response => {
                    if (response.data.success) {
                        // console.log(':', response.data.message);
                        this.$toast.success('');

                        this.$router.go(-2); //  replace
                    } else {
                        console.error(':', response.data.message);
                        this.$toast.fail('');
                    }
                })
                .catch(error => {
                    console.error(':', error);
                    this.$toast.fail('');
                });
        },

        handleItemClick(item) {
            if (this.isSpecialCity) {
                this.selectedDistrict = item.name;
                // console.log(':', item.name);
            } else {
                this.selectedCity = item.name;
                // console.log(':', item.name);
            }
        },

        getRegionDataByProvince(provinceName) {
            const provinceItem = regionData.find(item => Object.keys(item)[0] === provinceName);

            if (!provinceItem) {
                // console.error(`: ${provinceName}`);
                return []; // 
            }

            const provinceData = provinceItem[provinceName];

            //  provinceData. 
            if (!provinceData || !Array.isArray(provinceData.)) {
                console.error(` ${provinceName} :`, provinceData);
                return [];
            }

            //  ()
            //  ""  "name" 
            if (provinceData..length === 1 && provinceData.[0] && provinceData.[0].name === provinceName) {
                this.isSpecialCity = true;
                // 
                if (provinceData.[0]. && Array.isArray(provinceData.[0].)) {
                    return provinceData.[0].;
                } else {
                    console.warn(` ${provinceName} :`, provinceData.[0]);
                    return [];
                }
            } else {
                this.isSpecialCity = false;
                return provinceData.;
            }
        }
    },
    created() {
        this.provinceName = this.$route.query.province;
        if (this.provinceName) {
            const data = this.getRegionDataByProvince(this.provinceName);
            if (this.isSpecialCity) {
                this.districtList = data;
                // console.log(' ():', this.districtList);
                // 
                this.selectedCity = this.provinceName;
            } else {
                this.cityList = data;
                // console.log(' ():', this.cityList);
            }
        }
    }
};
</script>

