<template>
    <transition-group name="fade" tag="div" class="global_error" v-if="limitedErrors.length">
        <div class="error_li" v-for="(err, index) in limitedErrors" :key="index" :class="err.type">
            <span class="close_btn" @click="remove(index)">
                <Clear class="err-clear-area" :class="err.type">
                    <!-- <div class="clear"></div> -->
                </Clear>
            </span>
            <span class="err_mas">{{ err.msg }}</span>
        </div>
    </transition-group>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';

export default {
    name: 'GlobalError',
    data() {
        return {
             errorMaxWidth: '300px' //  mounted 
        }
    },
    computed: {
        ...mapGetters('error', ['allErrors']),
        limitedErrors() {
            return this.allErrors.slice(0, 2);
        }
    },
    methods: {
        ...mapMutations('error', ['REMOVE_ERROR']),
        remove(index) {
            this.REMOVE_ERROR(index);
        },
        checkEnvironmentAndSetWidth() {
            const userAgent = navigator.userAgent || navigator.vendor || window.opera;

            //  WebView 
            const isMobile = /android|iphone|ipad|ipod|blackberry|windows phone/i.test(userAgent);
            const isWebView = /wv|webview|ipod|iphone|ipad|android.*(wv)/i.test(userAgent) || (window.navigator.standalone === true);

            // 
            const isSmallScreen = window.innerWidth <= 768; // 768px /

            if (isMobile || isWebView || isSmallScreen) {
                // H5WebView
                this.errorMaxWidth = '90%'; // 90%
            } else {
                // 
                this.errorMaxWidth = '400px'; // 400px
            }
            // console.log(`.vue = Current environment: ${this.errorMaxWidth}`);
        }
    },
    mounted() {
        this.checkEnvironmentAndSetWidth();
        // 
        window.addEventListener('resize', this.checkEnvironmentAndSetWidth);
    },
    beforeDestroy() {
        // 
        window.removeEventListener('resize', this.checkEnvironmentAndSetWidth);
    }
};
</script>

<style lang="less" scoped>
.fade-enter-active,
.fade-leave-active {
    transition: all 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
    transform: translateY(-10px);
}

.global_error {
    position: fixed;
    top: calc(@nav + 20px);
    left: 50%;
    transform: translateX(-50%);
    z-index: 9999;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.error_li {
    // max-width: 300px;
    width: fit-content;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: @fontSize14;
    font-weight: bold;
    padding: 0 12px;
    border-radius: 10px;
    position: relative;

    &.error {
        background-color: rgb(255, 240, 240);
        border: 1px solid rgb(255, 160, 160);
        color: rgb(255, 120, 120);
    }

    &.success {
        background-color: rgb(240, 255, 240);
        border: 1px solid rgb(100, 200, 100);
        color: rgb(60, 180, 60);
    }

    .close_btn {
        cursor: pointer;
        font-weight: bold;
        margin-left: 6px;
    }

    .err_mas {
        margin-left: 16px;
        margin-right: 6px;
    }
}

.err-clear-area {
    position: relative !important;
    width: auto !important;
    background-color: transparent !important;

    &.success /deep/ .clear {
        background-color: rgb(100, 200, 100);
    }

    &.error /deep/ .clear {
        background-color: rgb(255, 120, 120);
    }
}

/deep/ .clear {
    width: 16px;
    height: 16px;
}
</style>
