<template>
    <div class="SetUp_container">
        <Header :header_name="header_name"></Header>

        <ul class="setup_box">
            <li class="li-box" @click="exit">
                <div class="li">
                    <div class="li_text"></div>
                    <div class="li_icon">
                        <Forward></Forward>
                    </div>
                </div>
            </li>
            <li class="li-box" @click="deactivateAccount">
                <div class="li">
                    <div class="li_text"></div>
                    <div class="li_icon">
                        <Forward></Forward>
                    </div>
                </div>
            </li>
        </ul>

        <!--  -->
        <div v-if="showPrompt" class="prompt_box">
            <div class="prompt">
                <div class="prompt_title"></div>
                <div class="prompt_message"></div>
                <div class="prompt_input">
                    <input v-model="password" type="password" placeholder="" />
                </div>
                <div class="prompt_buttons">
                    <button @click="confirmDeactivate"></button>
                    <button @click="cancelDeactivate"></button>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: 'SetUp',
    data() {
        return {
            header_name: '',
            showPrompt: false, // 
            password: '', // 

            //   user 
            user: {
                name: '',
                avatar: '',
                nickname: '',
                email: '' // 
            }
        };
    },
    computed: {
        //   Vuex getters 
        ...mapGetters(['currentUser', 'currentUserId', 'currentUserCustomId']),
    },
    methods: {
        ...mapActions(["logout"]), //  Vuex  logout action

        // 
        //  Vuex  currentUser  API 
        fetchUserProfile() {
            //  Vuex  Vuex 
            if (this.currentUser) {
                this.user.name = this.currentUser.name;
                this.user.avatar = `http://localhost:3081/${this.currentUser.avatar}`;
                this.user.nickname = this.currentUser.nickname;
                this.user.email = this.currentUser.email; // 
                console.log("SetUp==Vuex:", this.user);
            } else {
                //  Vuex  API 
                this.$axios.get('http://localhost:3081/api/user/profile')
                    .then(response => {
                        console.log("SetUp==API:", response.data);
                        if (response.data.success && response.data.user) {
                            //   user 
                            this.user.name = response.data.user.name;
                            this.user.avatar = `http://localhost:3081/${response.data.user.avatar}`;
                            this.user.nickname = response.data.user.nickname;
                            this.user.email = response.data.user.email; // 

                            //  Vuex store
                            this.$store.dispatch('loginSuccess', response.data.user);
                        } else {
                            console.warn(':', response.data.message);
                            this.$toast(response.data.message || '');
                        }
                    })
                    .catch(error => {
                        console.error(":", error);
                        this.$toast("");
                    });
            }
        },

        async exit() {
            try {
                await this.$axios.post(
                    "http://localhost:3081/api/exit",
                    {},
                    { withCredentials: true }
                );
                this.logout(); //  Vuex actions  logout
                this.$router.push("/Login");
            } catch (error) {
                console.error("", error);
                this.$toast("");
            }
        },

        deactivateAccount() {
            this.showPrompt = true;
        },

        // 
        async confirmDeactivate() {
            if (!this.password) {
                this.$toast("");
                return;
            }

            //   Vuex  user_id
            const user_Id = this.currentUserCustomId; //  liuhu_Ob70MyylIdqKOu
            //  ID this.currentUserId

            if (!user_Id) {
                this.$toast("ID");
                console.error("Vuex store ID");
                this.showPrompt = false;
                return;
            }

            try {
                const response = await this.$axios.post(
                    "http://localhost:3081/api/deactivate-account",
                    { userId: user_Id, password: this.password }, // 
                    { withCredentials: true }
                );

                if (response.data.success) {
                    this.$toast("");
                    this.logout(); //  Vuex 
                    this.$router.push("/Login");
                } else {
                    this.$toast(response.data.message);
                }
            } catch (error) {
                console.error("", error);
                this.$toast(error.response?.data?.message || "");
            } finally {
                this.showPrompt = false;
                this.password = '';
            }
        },

        cancelDeactivate() {
            this.showPrompt = false;
            this.password = '';
        }
    },
    created() {
        // 
        this.fetchUserProfile();
    },
};

</script>

