// src/store/error.js

const state = {
  errors: []  //  error = { msg: '', type: 'error' | 'success' }
};

const mutations = {
  ADD_ERROR(state, payload) {
    state.errors.push(payload);
  },
  REMOVE_ERROR(state, index = 0) {
    state.errors.splice(index, 1);
  }
};

const actions = {
  addError({ commit }, { msg, type = 'error' }) {
    const payload = { msg, type };
    commit('ADD_ERROR', payload);

    setTimeout(() => {
      commit('REMOVE_ERROR');
    }, 1000); // 
  }
};

const getters = {
  allErrors: state => state.errors
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};

