<template>
    <div class="UserNickName_container">
        <Header :header_name="header_name" @save="saveName"></Header>

        <div class="input-area">
            <div class="input-box" ref="inputBox">
                <input type="text" class="input" ref="input" name="nickname" v-model="user.nickname" autocapitalize="off" autocomplete="off" />
            </div>
            <div class="text"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'UserNickName',
    data() {
        return {
            header_name: '',
            user: {
                nickname: '' // 
            }
        };
    },
    methods: {
        //  
        fetchUserProfile() {
            // 
            this.$axios.get('/user/profile', { withCredentials: true }) // 
                .then(response => {
                    // console.log(":", response.data);
                    //  { success: true, user: { nickname: '...', ... } }
                    if (response.data.success && response.data.user) {
                        this.user.nickname = response.data.user.nickname; //   nickname
                    } else {
                        console.warn(":", response.data);
                    }
                })
                .catch(error => {
                    console.error(":", error);
                    // 
                    this.$toast.error('');
                });
        },
        // 
        saveName() {
            // 
            // http://localhost:3081/api/user/profile/update
            this.$axios.post('/user/profile/update', {
                nickname: this.user.nickname
            }, { withCredentials: true })
                .then(data => {
                    this.$toast.success('');
                    //  Vuex/
                    // : this.$store.commit('setNickname', this.user.nickname);
                    this.$router.go(-1); //  settings 
                })
                .catch(err => {
                    console.error('', err);
                    this.$toast.error('');
                });
        }
    },
    created() {
        //  
        this.fetchUserProfile();
    },
    mounted() {
        // 
        //  fetchUserProfile 
        //  focus
        this.$refs.input.focus();
    }
};
</script>

