<template>
    <!-- App.vue -->
    <div id="app">
        <!--  -->
        <router-view v-if="!isLoggedIn" />

        <!--  -->
        <GlobalError />

        <!--  -->
        <div class="app-container" v-if="isLoggedIn">
            <!--  -->
            <div class="sidebar_container">
                <div class="sidebar-content">
                    <div style="color: dodgerblue;">liuhu</div>
                    <!--  -->
                    <div class="user-avatar-container s1">
                        <img :src="getAvatarUrl(currentUser.avatar)" alt="" class="user-sidebar-avatar"
                            v-if="currentUser && currentUser.avatar" />
                        <img src="http://localhost:3081/public/default_user-avator/default.webp" alt=""
                            class="user-sidebar-avatar" v-else />
                    </div>

                    <van-tabbar class="van-tabbar" route :border="false">
                        <van-tabbar-item to="/Home" :icon="homeIcon"></van-tabbar-item>
                    </van-tabbar>

                    <van-tabbar class="van-tabbar" route :border="false">
                        <van-tabbar-item to="/Friends" :icon="friendsIcon"></van-tabbar-item>
                    </van-tabbar>

                    <van-tabbar class="van-tabbar" route :border="false">
                        <van-tabbar-item to="/Setting" :icon="settingIcon"></van-tabbar-item>
                    </van-tabbar>
                </div>
            </div>

            <!--  -->
            <div class="app-content">
                <!--  Home.vue -->
                <router-view />
            </div>
        </div>
    </div>
</template>


<script>
import { mapGetters, mapActions } from 'vuex';

export default {
    name: "App",
    data() {
        return {};
    },
    mounted() {
    },

    computed: {
        // 
        ...mapGetters(["isLoggedIn", "currentUser"]), // <--  currentUser getter

        //  
        homeIcon() {
            const currentPath = this.$route.path;
            // console.log('APP ===== Current route path for Home icon:', currentPath);
            return currentPath === '/Home' ? 'chat' : 'chat-o';
        },

        //  
        settingIcon() {
            //  '/Setting' 'setting' 'setting-o'
            return this.$route.path === '/Setting' ? 'setting' : 'setting-o';
        },

        //  
        friendsIcon() {
            //  '/Friends' 'friends' 'friends-o'
            return this.$route.path === '/Friends' ? 'friends' : 'friends-o';
        }
    },
    created() {
        this.checkLogin();
    },
    methods: {
        ...mapActions(["checkLogin"]),
        //  ChatArea.vue  getAvatarUrl 
        getAvatarUrl(relativePath) {
            //  relativePath null
            if (!relativePath) {
                return 'http://localhost:3081/public/default_user-avator/default.webp';
            }

            //  relativePath  URL ( http://...)
            // 
            if (relativePath.startsWith('http://') || relativePath.startsWith('https://')) {
                return relativePath;
            }

            //  relativePath  /uploads/...  public/...
            const baseUrl = 'http://localhost:3081';
            //  '/'  /public/  /uploads/ 
            if (!relativePath.startsWith('/')) {
                relativePath = '/' + relativePath;
            }
            return `${baseUrl}${relativePath}`;
        },
    }
};
</script>

