<template>
    <!-- user_name.vue -->
    <div class="user_name-container">
        <!--  header_name  Header  -->
        <Header :header_name="header_name" @save="saveName"></Header>

        <div class="input-area">
            <div class="input-box" ref="inputBox">
                <input type="text" class="input" ref="input" name="user-name" v-model="user.name" />
            </div>
            <div class="text"></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'UserName',
    data() {
        return {
            header_name: '',
            user: {
                name: '',
            }
        }
    },
    methods: {
        // 
        fetchUserProfile() {
            // http://localhost:3081/api/user/profile
            this.$axios.get('/user/profile')
                .then(response => {
                    // console.log(":", response.data);
                    //  { success: true, user: { name: '...', ... } }
                    if (response.data.success && response.data.user) {
                        this.user.name = response.data.user.name; //  
                    } else {
                        console.warn(":", response.data);
                    }
                })
                .catch(error => {
                    console.error(":", error);
                });
        },
        // 
        saveName() {
            // 
            // http://localhost:3081/api/user/profile/update
            this.$axios.post('/user/profile/update', {
                name: this.user.name
            }, { withCredentials: true })
                .then(data => {
                    this.$toast.success('');
                    this.$router.go(-1); //  settings 
                })
                .catch(err => {
                    console.error('', err);
                });
        }
    },
    created() {
        this.fetchUserProfile();
    },
}
</script>

