// src/router/index.js
import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'; //  Vuex store
import axios from 'axios'; //  axios  session

// 
import Home from '@/view/home.vue'
import Setting from '@/view/setting.vue'
// import UserList from '@/view/UserList.vue'  // 
// import ChatArea from '@/view/ChatArea.vue'  // 

// 
import index from '@/loginPage/index.vue'
import Login from '@/loginPage/login.vue'
import Register from '@/loginPage/register.vue'
import ForgetPassword from '@/loginPage/ForgetPassword.vue'; //  ForgetPassword

// 
import PersonalInformation from '@/settingPages/PersonalInformation.vue'
// 
import ChangePassword from '@/settingPages/ChangePassword.vue'
// 
import SetNewPassword from '@/settingPages/SetNewPassword.vue'
// 
import Friends from '@/view/Friends.vue';

// 
import UserName from '@/settingPages/p_i_pages/UserName.vue';
import UserGender from '@/settingPages/p_i_pages/UserGender.vue';
import UserArea from '@/settingPages/p_i_pages/UserArea.vue';
import UserAreaDetail from '@/settingPages/p_i_pages/UserAreaDetail.vue';
import UserNickName from '@/settingPages/p_i_pages/UserNickName.vue';
import SetUp from '@/settingPages/SetUp.vue';

//  VueRouter 
Vue.use(VueRouter)

const routes = [
    //  
    { path: '/', name: '/', component: index, meta: { requiresGuest: true } },
    { path: '/index', name: 'index', component: index, meta: { requiresGuest: true } },
    { path: '/Login', name: 'Login', component: Login, meta: { requiresGuest: true } },
    { path: '/Register', name: 'Register', component: Register, meta: { requiresGuest: true } },
    { path: '/ForgetPassword', name: 'ForgetPassword', component: ForgetPassword, meta: { requiresGuest: true, requiresForgotPasswordVerification: true } }, //  requiresForgotPasswordVerification

    //  
    { path: '/Home', name: 'Home', component: Home, meta: { requiresAuth: true } },
    { path: '/Setting', name: 'Setting', component: Setting, meta: { requiresAuth: true } },

    // 
    { path: '/PersonalInformation', name: 'PersonalInformation', component: PersonalInformation, meta: { requiresAuth: true } },
    // 
    { path: '/ChangePassword', name: 'ChangePassword', component: ChangePassword, meta: { requiresAuth: true } },
    // 
    { path: '/SetNewPassword', name: 'SetNewPassword', component: SetNewPassword, meta: { requiresAuth: true } },
    // 
    { path: '/SetUp', name: 'SetUp', component: SetUp, meta: { requiresAuth: true } },
    // 
    { path: '/Friends', name: 'Friends', component: Friends, meta: { requiresAuth: true } },

    // 
    { path: '/UserName', name: 'UserName', component: UserName, meta: { requiresAuth: true } },
    { path: '/UserGender', name: 'UserGender', component: UserGender, meta: { requiresAuth: true } },
    { path: '/UserArea', name: 'UserArea', component: UserArea, meta: { requiresAuth: true } },
    { path: '/UserAreaDetail', name: 'UserAreaDetail', component: UserAreaDetail, meta: { requiresAuth: true }, props: true },
    { path: '/UserNickName', name: 'UserNickName', component: UserNickName, meta: { requiresAuth: true } },

];

const router = new VueRouter({
    mode: 'history',
    routes,  //  VueRouter
})

// session + cookie
router.beforeEach(async (to, from, next) => {
    const requiresAuth = to.matched.some(record => record.meta.requiresAuth);
    const requiresGuest = to.matched.some(record => record.meta.requiresGuest);
    // 
    const requiresForgotPasswordVerification = to.matched.some(record => record.meta.requiresForgotPasswordVerification);

    //  store 
    await store.dispatch('checkLogin');
    const loggedIn = store.getters.isLoggedIn;

    // 
    if (to.path === from.path && to.name === from.name) {
        return next();
    }

    //  /ForgetPassword 
    if (requiresForgotPasswordVerification) {
        try {
            const response = await axios.get('/check-forgot-password-verification', {
                withCredentials: true
            });
            if (!response.data.verified) {
                // 
                store.dispatch('error/addError', {
                    msg: '',
                    type: 'error'
                });
                return next('/Login'); // 
            }
        } catch (error) {
            console.error(':', error);
            store.dispatch('error/addError', {
                msg: '',
                type: 'error'
            });
            return next('/Login');
        }
    }

    // 
    if (requiresAuth && !loggedIn) {
        return next('/Login');
    }

    // 
    if (requiresGuest && loggedIn) {
        //  /Home 
        if (to.path !== '/Home') {
            return next('/Home');
        } else {
            //  /Home ( requiresGuest  true )
            //  /Home 
            return next();
        }
    }

    // console.log('router/index.js :', to.path);
    // 
    next();
});

export default router;  //  VueRouter 