<template>
    <div class="Forget_Password-container">
        <div class="head"></div>
        <form @submit.prevent="submitNewPassword" autocapitalize="off" autocomplete="off">
            <div class="content">
                <div class="content-item">
                    <div class="text"></div>
                    <input type="password" v-model="forget_newPassword" name="forget_new_password" placeholder="" required>
                </div>
            </div>

            <div class="content">
                <div class="content-item">
                    <div class="text"></div>
                    <input type="password" v-model="forget_confirmPassword" name="forget_confirm_password" placeholder="" required>
                </div>
            </div>
            <div class="btn_box">
                <button type="submit" class="submit"
                    :disabled="!forget_newPassword || !forget_confirmPassword || forget_newPassword !== forget_confirmPassword"></button>
            </div>
        </form>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'ForgetPassword',
    data() {
        return {
            email: this.$route.query.email || '', // 
            forget_newPassword: '',
            forget_confirmPassword: ''
        };
    },
    async created() {
        // 
        // try {
        //     const response = await axios.get('/check-forgot-password-verification', {
        //         withCredentials: true
        //     });
        //     // 
        //     if (!response.data.verified || response.data.email !== this.email) {
        //         this.$store.dispatch('error/addError', {
        //             msg: '',
        //             type: 'error'
        //         });
        //         this.$router.replace('/Login'); // 
        //     }
        // } catch (error) {
        //     console.error(':', error);
        //     this.$store.dispatch('error/addError', {
        //         msg: error.response?.data?.message || '',
        //         type: 'error'
        //     });
        //     this.$router.replace('/Login');
        // }
    },
    methods: {
        async submitNewPassword() {
            // 
            if (!this.forget_newPassword) { // 
                this.$store.dispatch('error/addError', {
                    msg: '',
                    type: 'error'
                });
                return;
            }

            if (!this.forget_confirmPassword) { // 
                this.$store.dispatch('error/addError', {
                    msg: '', // 
                    type: 'error'
                });
                return;
            }

            if (this.forget_newPassword !== this.forget_confirmPassword) {
                this.$store.dispatch('error/addError', {
                    msg: '',
                    type: 'error'
                });
                return;
            }
            if (this.forget_newPassword.length < 6) {
                this.$store.dispatch('error/addError', {
                    msg: '6',
                    type: 'error'
                });
                return;
            }

            try {
                const response = await axios.post('http://localhost:3081/api/set-new-password-forgotten', {
                    email: this.email,
                    newPassword: this.forget_newPassword
                }, {
                    withCredentials: true
                });

                if (response.data.success) {
                    this.$store.dispatch('error/addError', {
                        msg: '',
                        type: 'success'
                    });
                    // 
                    this.$router.push('/Login');
                } else {
                    this.$store.dispatch('error/addError', {
                        msg: response.data.message || '',
                        type: 'error'
                    });
                }
            } catch (error) {
                console.error(':', error);
                this.$store.dispatch('error/addError', {
                    msg: error.response?.data?.message || '',
                    type: 'error'
                });
            }
        }
    }
};
</script>

