<template>
    <div class="UserArea-container">
        <Header :header_name="header_name"></Header>

        <div class="user_area_box">
            <div class="title_text"></div>
            <div class="current_location_box">
                <div class="location_icon">
                    <van-icon size="20" name="location" />
                </div>
                <div class="location_text" v-if="user && user.region">{{ user.region }}</div>
                <div class="location_text" v-else>...</div>
            </div>
        </div>

        <div class="select_region_box">
            <div class="select_region_title"></div>
            <ul class="select_region_content">
                <li class="region_li" v-for="(provinceItem, index) in regionData" :key="index"
                    @click="handleClick(provinceItem)">
                    <div class="li">
                        <div class="li_text">{{ getProvinceName(provinceItem) }}</div>
                        <div class="li_right">
                            <div class="current_region" v-if="isSelected(provinceItem)"></div>
                            <div class="li_forward">
                                <Forward></Forward>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import regionData from '../../../region.json';

export default {
    name: 'UserArea',
    data() {
        return {
            regionData,
            header_name: '',
            //   user 
            user: {
                region: '', // 
            },
            selectedRegion: '', // 
        };
    },
    computed: {
        provinceList() {
            return this.regionData;
        },
        //  
        currentDisplayRegion() {
            return this.user.region || '...';
        }
    },
    methods: {
        getProvinceName(provinceItem) {
            return Object.keys(provinceItem)[0];
        },
        handleClick(provinceItem) {
            const provinceName = this.getProvinceName(provinceItem);
            this.selectedRegion = provinceName;
            this.$router.push({ path: '/UserAreaDetail', query: { province: provinceName } });
            // console.log("UserArea--:", provinceItem);
        },
        isSelected(provinceItem) {
            const currentProvinceName = this.getProvinceName(provinceItem);

            //  `selectedRegion`
            if (this.selectedRegion) {
                return currentProvinceName === this.selectedRegion;
            }

            //  `user.region` 
            if (this.user.region) {
                const userProvince = this.user.region.split(' ')[0];
                return currentProvinceName === userProvince;
            }

            return false;
        },
        // 
        fetchUserProfile() {
            this.$axios.get('/user/profile')
                .then(response => {
                    // console.log("UserArea :", response.data);
                    //   user.region
                    //  response.data.user  region 
                    if (response.data && response.data.user && response.data.user.region) {
                         this.$set(this.user, 'region', response.data.user.region); //  $set 
                        // this.user.region = response.data.user.region; //  $set 
                        
                        //  selectedRegion
                        this.selectedRegion = response.data.user.region.split(' ')[0];
                    } else {
                        // 
                        this.user.region = '';
                        this.selectedRegion = '';
                    }
                })
                .catch(error => {
                    console.error(":", error);
                    this.$toast.fail(''); // 
                    this.user.region = '';
                });
        },
    },
    created() {
        // 
        // fetchUserProfile  user.region  selectedRegion
        this.fetchUserProfile(); 
        
        //  UserAreaDetail  lastSelectedProvince 
        if (this.$route.query.lastSelectedProvince) {
            //  selectedRegion fetchUserProfile 
            this.selectedRegion = this.$route.query.lastSelectedProvince;
            // 
            this.$router.replace({ query: { ...this.$route.query, lastSelectedProvince: undefined } }).catch(()=>{}); // catch
        }
    },
    watch: {
        //  ( UserAreaDetail )
        '$route.query.lastSelectedProvince'(newVal) {
            if (newVal) {
                this.selectedRegion = newVal;
                // 
                this.fetchUserProfile(); 
                this.$router.replace({ query: { ...this.$route.query, lastSelectedProvince: undefined } }).catch(()=>{});
            }
        },
        //  user.region  selectedRegion 
        //  watch user.region 
        //  watch  selectedRegion 
        'user.region'(newVal) {
            if (newVal) {
                this.selectedRegion = newVal.split(' ')[0];
            } else {
                this.selectedRegion = '';
            }
        }
    }
};
</script>

